package com.sefagurel.komposto.data.model

import com.sefagurel.komposto.data.Sn
import kotlinx.serialization.SerialName
import kotlinx.serialization.Serializable

@Serializable
@SerialName(Sn.Shape)
sealed interface ShapeData {
    @Serializable
    @SerialName(Sn.RectangleShape)
    data object RectangleShape : ShapeData

    @Serializable
    @SerialName(Sn.CircleShape)
    data object CircleShape : ShapeData

    @Serializable
    @SerialName(Sn.RoundedCornerShape)
    data class RoundedCornerShape(
        @SerialName(Sn.TopStart)
        val topStart: Float? = null,
        @SerialName(Sn.TopEnd)
        val topEnd: Float? = null,
        @SerialName(Sn.BottomEnd)
        val bottomEnd: Float? = null,
        @SerialName(Sn.BottomStart)
        val bottomStart: Float? = null
    ) : ShapeData
}