package com.sefagurel.komposto.ui.component

import androidx.compose.foundation.BorderStroke
import androidx.compose.foundation.border
import androidx.compose.foundation.layout.Column
import androidx.compose.foundation.layout.Spacer
import androidx.compose.foundation.layout.size
import androidx.compose.foundation.shape.RoundedCornerShape
import androidx.compose.material.Text
import androidx.compose.runtime.Composable
import androidx.compose.runtime.LaunchedEffect
import androidx.compose.runtime.rememberCoroutineScope
import androidx.compose.ui.Alignment
import androidx.compose.ui.Modifier
import androidx.compose.ui.draw.clip
import androidx.compose.ui.graphics.Color
import androidx.compose.ui.layout.ContentScale
import androidx.compose.ui.text.style.TextAlign
import androidx.compose.ui.text.style.TextOverflow
import androidx.compose.ui.unit.dp
import com.sefagurel.komposto.ui.HydraClick
import com.sefagurel.komposto.ui.extension.addCommonModifiers
import com.sefagurel.komposto.ui.extension.isNull
import com.sefagurel.komposto.ui.extension.orFalse
import com.sefagurel.komposto.ui.extension.sendEventWithSearchTerm
import com.sefagurel.komposto.ui.extension.toColor
import com.sefagurel.komposto.ui.model.HydraEvent
import com.sefagurel.komposto.ui.model.HydraEventType
import com.sefagurel.komposto.ui.model.PosterBoxCustomUiModel
import com.sefagurel.komposto.ui.model.PosterBoxDataUiModel
import com.sefagurel.komposto.ui.model.UIModel
import com.sefagurel.komposto.ui.ui.HydraColor
import com.sefagurel.komposto.ui.ui.HydraTheme
import kotlinx.coroutines.launch

@Composable
internal fun PosterBoxUI(
    scope: Any? = null,
    uiModel: UIModel,
    onClick: (HydraClick) -> Unit,
    onEvent: (HydraEvent) -> Unit
) {
    val posterBoxModel = uiModel.current ?: return
    val parent = uiModel.parent
    val posterBoxDataModel = posterBoxModel.data as? PosterBoxDataUiModel ?: return
    val posterBoxCustomModel = posterBoxModel.custom as? PosterBoxCustomUiModel
    val coroutineScope = rememberCoroutineScope()
    val text = posterBoxDataModel.text
    val imageUrl = posterBoxDataModel.imageUrl

    if (imageUrl.isNull() && text.isNull()) return

    LaunchedEffect(key1 = parent?.searchTerm?.value) {
        coroutineScope.launch {
            posterBoxModel.base?.events?.find { it.type == HydraEventType.View }
                ?.let { hydraEvent ->
                    hydraEvent.sendEventWithSearchTerm(
                        onEvent = onEvent,
                        searchTerm = parent?.searchTerm?.value
                    )
                }
        }
    }

    Column(
        modifier = Modifier
            .addCommonModifiers(
                uiModel = uiModel,
                scope = scope,
                onClick = {
                    coroutineScope.launch {
                        posterBoxModel.base?.events?.find { it.type == HydraEventType.Click }
                            ?.let { hydraEvent ->
                                hydraEvent.sendEventWithSearchTerm(
                                    onEvent = onEvent,
                                    searchTerm = parent?.searchTerm?.value
                                )
                            }
                    }
                    onClick.invoke(it)
                }
            ),
        horizontalAlignment = Alignment.CenterHorizontally
    ) {
        HydraImage(
            url = imageUrl.orEmpty(),
            contentDescription = null,
            contentScale = ContentScale.Fit,
            modifier = Modifier
                .weight(1f)
                .then(
                    if (posterBoxCustomModel?.showImageBorder.orFalse()) {
                        Modifier
                            .border(
                                border = BorderStroke(
                                    width = 1.dp,
                                    color = HydraColor.BorderColor
                                ),
                                shape = RoundedCornerShape(8.dp)
                            )
                            .clip(RoundedCornerShape(8.dp))
                    } else {
                        Modifier
                    }
                )
        )

        Spacer(modifier = Modifier.size(2.dp))

        Text(
            text = text.orEmpty(),
            style = HydraTheme.font.h6Semibold().copy(
                color = posterBoxCustomModel?.titleColor?.toColor() ?: Color.Black,
                textAlign = TextAlign.Center
            ),
            minLines = posterBoxCustomModel?.numberOfLines.getNumberOfLines(),
            maxLines = posterBoxCustomModel?.numberOfLines.getNumberOfLines(),
            overflow = TextOverflow.Ellipsis
        )

    }
}

private fun Int?.getNumberOfLines(): Int {
    return when (this) {
        0, null -> 1
        else -> this
    }
}
