package com.sefagurel.komposto.webpanel.z_other.component

import androidx.compose.foundation.background
import androidx.compose.foundation.gestures.Orientation
import androidx.compose.foundation.gestures.draggable
import androidx.compose.foundation.gestures.rememberDraggableState
import androidx.compose.foundation.layout.Box
import androidx.compose.foundation.layout.BoxScope
import androidx.compose.foundation.layout.Row
import androidx.compose.foundation.layout.fillMaxHeight
import androidx.compose.foundation.layout.height
import androidx.compose.foundation.layout.width
import androidx.compose.foundation.shape.RoundedCornerShape
import androidx.compose.runtime.Composable
import androidx.compose.runtime.MutableState
import androidx.compose.runtime.getValue
import androidx.compose.runtime.mutableStateOf
import androidx.compose.runtime.remember
import androidx.compose.runtime.setValue
import androidx.compose.ui.Alignment
import androidx.compose.ui.Modifier
import androidx.compose.ui.graphics.Color
import androidx.compose.ui.layout.onGloballyPositioned
import androidx.compose.ui.unit.Dp
import androidx.compose.ui.unit.dp


@Composable
internal fun ResizableTwoComponent(
    modifier: Modifier,
    leftWeight: Float = 0.7f,
    rightWeight: Float = 0.3f,
    leftComponent: @Composable BoxScope.() -> Unit,
    rightComponent: @Composable BoxScope.() -> Unit,
    dividerColor: Color = Color.Gray,
    dividerWidth: Dp = 4.dp
) {
    var weightLeft by remember { mutableStateOf(leftWeight) }
    var weightRight by remember { mutableStateOf(rightWeight) }
    var parentWidth by remember { mutableStateOf(0f) }

    Row(
        modifier = modifier.onGloballyPositioned {
            parentWidth = it.size.width.toFloat()
        }
    ) {
        Box(
            modifier = Modifier.weight(weightLeft),
            content = leftComponent
        )

        if (dividerWidth > 0.dp) {
            Box(
                modifier = Modifier
                    .fillMaxHeight()
                    .width(dividerWidth)
                    .background(dividerColor)
                    .draggable(
                        orientation = Orientation.Horizontal,
                        state = rememberDraggableState { delta ->
                            val scaleFactor = parentWidth
                            val newDelta = delta / scaleFactor
                            weightLeft = (weightLeft + newDelta).coerceIn(0.1f, 0.9f)
                            weightRight = (weightRight - newDelta).coerceIn(0.1f, 0.9f)
                        }
                    )
            ) {
                val width = dividerWidth * 0.5f

                Box(
                    modifier = Modifier
                        .align(Alignment.Center)
                        .width(width)
                        .height(width * 8)
                        .background(
                            color = Color.White,
                            shape = RoundedCornerShape(size = 64.dp)
                        )
                        .background(
                            color = dividerColor.copy(alpha = 0.4f),
                            shape = RoundedCornerShape(size = 64.dp)
                        )
                )
            }
        }

        Box(
            modifier = Modifier.weight(weightRight),
            content = rightComponent
        )
    }
}

@Composable
fun ResizableThreeComponent(
    modifier: Modifier,
    leftWeight: Float = 0.2f,
    centerWeight: Float = 0.5f,
    rightWeight: Float = 0.3f,
    leftComponent: @Composable BoxScope.() -> Unit,
    centerComponent: @Composable BoxScope.() -> Unit,
    rightComponent: @Composable BoxScope.() -> Unit,
    dividerColor: Color = Color.Gray,
    dividerWidth: Dp = 4.dp
) {
    val weightLeft = remember { mutableStateOf(leftWeight) }
    val weightCenter = remember { mutableStateOf(centerWeight) }
    val weightRight = remember { mutableStateOf(rightWeight) }
    var parentWidth by remember { mutableStateOf(0f) }

    Row(
        modifier = modifier.onGloballyPositioned {
            parentWidth = it.size.width.toFloat()
        }
    ) {
        Box(
            modifier = Modifier.weight(weightLeft.value),
            content = leftComponent
        )

        if (dividerWidth > 0.dp) {
            draggableDivider(
                weightLeft,
                weightCenter,
                parentWidth,
                dividerColor,
                dividerWidth,
                lowerBound = 0.1f,
                upperBound = 0.9f - weightRight.value
            )
        }

        Box(
            modifier = Modifier.weight(weightCenter.value),
            content = centerComponent
        )

        if (dividerWidth > 0.dp) {
            draggableDivider(
                weightCenter,
                weightRight,
                parentWidth,
                dividerColor,
                dividerWidth,
                lowerBound = 0.1f,
                upperBound = 0.9f - weightLeft.value
            )
        }

        Box(
            modifier = Modifier.weight(weightRight.value),
            content = rightComponent
        )
    }
}


@Composable
internal fun draggableDivider(
    weightStart: MutableState<Float>,
    weightEnd: MutableState<Float>,
    parentWidth: Float,
    dividerColor: Color,
    dividerWidth: Dp,
    lowerBound: Float,
    upperBound: Float
) {
    Box(
        modifier = Modifier
            .fillMaxHeight()
            .width(dividerWidth)
            .background(dividerColor)
            .draggable(
                orientation = Orientation.Horizontal,
                state = rememberDraggableState { delta ->
                    val scaleFactor = parentWidth
                    val newDelta = delta / scaleFactor
                    val startValue = weightStart.value
                    val endValue = weightEnd.value
                    val totalValue = startValue + endValue
                    weightStart.value = (startValue + newDelta).coerceIn(lowerBound, upperBound)
                    weightEnd.value = totalValue - weightStart.value
                }
            )
    ) {
        val width = dividerWidth * 0.5f

        Box(
            modifier = Modifier
                .align(Alignment.Center)
                .width(width)
                .height(width * 8)
                .background(
                    color = Color.White,
                    shape = RoundedCornerShape(size = 64.dp)
                )
                .background(
                    color = dividerColor.copy(alpha = 0.4f),
                    shape = RoundedCornerShape(size = 64.dp)
                )
        )
    }
}


