package com.sefagurel.komposto.data.serializer

import com.sefagurel.komposto.data.Sn
import com.sefagurel.komposto.data.model.AlignmentData
import com.sefagurel.komposto.data.model.HorizontalAlignmentData
import com.sefagurel.komposto.data.model.VerticalAlignmentData
import kotlinx.serialization.KSerializer
import kotlinx.serialization.descriptors.PrimitiveKind
import kotlinx.serialization.descriptors.PrimitiveSerialDescriptor
import kotlinx.serialization.descriptors.SerialDescriptor
import kotlinx.serialization.encoding.Decoder
import kotlinx.serialization.encoding.Encoder

// For DCHorizontalAlignment
object HorizontalAlignmentDataSerializer : KSerializer<HorizontalAlignmentData> {
    override val descriptor: SerialDescriptor =
        PrimitiveSerialDescriptor("HorizontalAlignmentData", PrimitiveKind.STRING)

    override fun serialize(encoder: Encoder, value: HorizontalAlignmentData) {
        when (value) {
            is AlignmentData.Start -> encoder.encodeString(Sn.Start)
            is AlignmentData.CenterHorizontally -> encoder.encodeString(Sn.CenterHorizontally)
            is AlignmentData.End -> encoder.encodeString(Sn.End)
        }
    }

    override fun deserialize(decoder: Decoder): HorizontalAlignmentData {
        return when (val value = decoder.decodeString()) {
            Sn.Start -> AlignmentData.Start
            Sn.CenterHorizontally -> AlignmentData.CenterHorizontally
            Sn.End -> AlignmentData.End
            else -> throw IllegalArgumentException("Unknown value: $value")
        }
    }
}

// For DCVerticalAlignment
