}

// For DCAlignment
object AlignmentDataSerializer : KSerializer<AlignmentData> {
    override val descriptor: SerialDescriptor
        get() {
            return PrimitiveSerialDescriptor("AlignmentData", PrimitiveKind.STRING)
        }

    override fun serialize(encoder: Encoder, value: AlignmentData) {
        when (value) {
            is AlignmentData.TopStart -> encoder.encodeString(Sn.TopStart)
            is AlignmentData.TopCenter -> encoder.encodeString(Sn.TopCenter)
            is AlignmentData.TopEnd -> encoder.encodeString(Sn.TopEnd)
            is AlignmentData.CenterStart -> encoder.encodeString(Sn.CenterStart)
            is AlignmentData.Center -> encoder.encodeString(Sn.Center)
            is AlignmentData.CenterEnd -> encoder.encodeString(Sn.CenterEnd)
            is AlignmentData.BottomStart -> encoder.encodeString(Sn.BottomStart)
            is AlignmentData.BottomCenter -> encoder.encodeString(Sn.BottomCenter)
            is AlignmentData.BottomEnd -> encoder.encodeString(Sn.BottomEnd)
        }
    }

    override fun deserialize(decoder: Decoder): AlignmentData {
        return when (val value = decoder.decodeString()) {
            Sn.TopStart -> AlignmentData.TopStart
            Sn.TopCenter -> AlignmentData.TopCenter
            Sn.TopEnd -> AlignmentData.TopEnd
            Sn.CenterStart -> AlignmentData.CenterStart
            Sn.Center -> AlignmentData.Center
            Sn.CenterEnd -> AlignmentData.CenterEnd
            Sn.BottomStart -> AlignmentData.BottomStart
            Sn.BottomCenter -> AlignmentData.BottomCenter
            Sn.BottomEnd -> AlignmentData.BottomEnd
            else -> throw IllegalArgumentException("Unknown value: $value")
        }
    }
}