@Composable
internal fun HydraButtonWithBorderComponent(
    modifier: Modifier = Modifier,
    text: String,
    textColor: Color? = null,
    textStyle: TextStyle = HydraTheme.font.h6Semibold(),
    borderWidth: Dp = 2.dp,
    borderColor: Color = HydraColor.BorderColor,
    borderRadius: Dp = 8.dp,
    backgroundColor: Color = Color.White,
    paddingValues: PaddingValues = PaddingValues(vertical = 6.dp, horizontal = 12.dp),
    onClickAction: () -> Unit = {},
    isEnabled: Boolean = true,
    maxLines: Int = Int.MAX_VALUE
) {
    Box(
        modifier = modifier
            .clip(RoundedCornerShape(borderRadius))
            .border(
                border = BorderStroke(
                    borderWidth,
                    borderColor
                ),
                shape = RoundedCornerShape(borderRadius)
            )
            .background(backgroundColor)
            .then(
                if (isEnabled) {
                    Modifier.clickable(
                        role = Role.Button,
                        onClick = onClickAction
                    )
                } else Modifier
            )
            .padding(paddingValues),
        contentAlignment = Alignment.Center
    ) {
        Text(
            text = text,
            style = textStyle,
            maxLines = maxLines
        )
    }
}

