@OptIn(ExperimentalResourceApi::class)
@Composable
internal fun HydraIconButtonComponentWithBorder(
    modifier: Modifier = Modifier,
    text: String,
    textStyle: TextStyle = HydraTheme.font.h6Semibold(),
    backgroundColor: Color = Color.White,
    paddingValues: PaddingValues = PaddingValues(vertical = 6.dp, horizontal = 12.dp),
    onClickAction: () -> Unit = {},
    borderWidth: Dp = 2.dp,
    borderColor: Color = HydraColor.BorderColor,
    borderRadius: Dp = 8.dp,
    isEnabled: Boolean = true,
    radius: Dp = 8.dp,
    spaceBetweenIconAndText: Dp = 4.dp,
    iconResId: DrawableResource = Res.drawable.hy_ic_favorite_list,
    iconWidth: Dp = 18.33.dp,
    iconHeight: Dp = 16.67.dp,
    iconTintColor: Color = HydraColor.BlackCow,
    maxLines: Int = Int.MAX_VALUE
) {
    Row(
        modifier = modifier
            .clip(RoundedCornerShape(radius))
            .border(
                border = BorderStroke(
                    width = borderWidth,
                    color = borderColor
                ),
                shape = RoundedCornerShape(borderRadius)
            )
            .background(backgroundColor)
            .then(
                if (isEnabled) {
                    Modifier.clickable(
                        role = Role.Button,
                        onClick = onClickAction
                    )
                } else Modifier
            )
            .padding(paddingValues),
        verticalAlignment = Alignment.CenterVertically,
        horizontalArrangement = Arrangement.spacedBy(
            space = spaceBetweenIconAndText,
            alignment = Alignment.CenterHorizontally
        )
    ) {
        Icon(
            modifier = Modifier.size(width = iconWidth, height = iconHeight),
            painter = painterResource(iconResId),
            contentDescription = null,
            tint = iconTintColor
        )

        Text(
            text = text,
            style = textStyle,
            maxLines = maxLines,
            overflow = TextOverflow.Ellipsis
        )
    }
}