package com.sefagurel.komposto.webpanel

import androidx.compose.foundation.Image
import androidx.compose.foundation.layout.Column
import androidx.compose.foundation.layout.Row
import androidx.compose.foundation.layout.Spacer
import androidx.compose.foundation.layout.fillMaxSize
import androidx.compose.foundation.layout.height
import androidx.compose.foundation.layout.padding
import androidx.compose.foundation.layout.size
import androidx.compose.foundation.layout.width
import androidx.compose.foundation.rememberScrollState
import androidx.compose.foundation.shape.CircleShape
import androidx.compose.foundation.verticalScroll
import androidx.compose.material.IconButton
import androidx.compose.runtime.Composable
import androidx.compose.runtime.LaunchedEffect
import androidx.compose.runtime.mutableStateOf
import androidx.compose.runtime.remember
import androidx.compose.runtime.rememberCoroutineScope
import androidx.compose.ui.ExperimentalComposeUiApi
import androidx.compose.ui.Modifier
import androidx.compose.ui.draw.clip
import androidx.compose.ui.graphics.Color
import androidx.compose.ui.unit.dp
import androidx.compose.ui.window.CanvasBasedWindow
import com.sefagurel.komposto.ui.fetchApiResponse
import com.sefagurel.komposto.ui.getRunIcon
import com.sefagurel.komposto.webpanel.settingsui.basic.TextFieldUI
import com.sefagurel.komposto.webpanel.settingsui.basic.TitleUI
import com.sefagurel.komposto.webpanel.ui.RightComponent
import com.sefagurel.komposto.webpanel.z_other.component.ResizableTwoComponent
import com.sefagurel.komposto.webpanel.z_other.provider.MessageBox
import com.sefagurel.komposto.webpanel.z_other.theme.WebPanelTheme
import kotlinx.coroutines.delay
import kotlinx.coroutines.launch
import org.jetbrains.skiko.wasm.onWasmReady

@OptIn(ExperimentalComposeUiApi::class)
fun main() {
    onWasmReady {
        CanvasBasedWindow("Hydra Panel") {
            WebPanelTheme {
                MainContent()
            }
        }
    }
}

@Composable
internal fun MainContent() {
    MessageBox {

//        https://hydra-api.hepsiburada.com/api/v1/view/elektronik-pager-search

        val pageId = remember { mutableStateOf<String?>("elektronik-pager-search") }
        val jsonUrl = remember { mutableStateOf<String?>(null) }
        val jsonData = remember { mutableStateOf<String?>(null) }

        val coroutineScope = rememberCoroutineScope()

        LaunchedEffect(pageId.value) {
            if (pageId.value == null) return@LaunchedEffect
            coroutineScope.launch {
                jsonData.value = runCatching {
                    fetchApiResponse("https://hydra-api.hepsiburada.com/api/v1/view/" + pageId.value)
                }.getOrDefault("{}")
            }
        }

        LaunchedEffect(jsonUrl.value) {
            if (jsonUrl.value == null) return@LaunchedEffect
            coroutineScope.launch {
                jsonData.value = runCatching {
                    fetchApiResponse(jsonUrl.value)
                }.getOrDefault("{}")
            }
        }

        ResizableTwoComponent(
            modifier = Modifier.fillMaxSize(),
            leftComponent = {
                Spacer(modifier = Modifier.width(16.dp))

                Column(
                    modifier = Modifier
                        .verticalScroll(rememberScrollState())
                        .padding(24.dp)
                ) {
                    TitleUI("Page Id")

                    Spacer(modifier = Modifier.height(8.dp))

                    val textId = remember { mutableStateOf(pageId.value) }

                    Row {
                        TextFieldUI(
                            modifier = Modifier.weight(1f).height(50.dp),
                            value = textId.value.orEmpty(),
                            onValueChange = {
                                textId.value = it
                            }
                        )
                        Spacer(modifier = Modifier.width(8.dp))
                        IconButton(
                            onClick = {
                                coroutineScope.launch {
                                    jsonData.value = null
                                    pageId.value = null
                                    delay(50)
                                    pageId.value = textId.value
                                }
                            }
                        ) {
                            Image(
                                modifier = Modifier
                                    .clip(CircleShape)
                                    .size(32.dp),
                                imageVector = getRunIcon(),
                                contentDescription = null
                            )
                        }
                    }

                    Spacer(modifier = Modifier.height(48.dp))

                    TitleUI("Url")

                    Spacer(modifier = Modifier.height(8.dp))

                    val textUrl = remember { mutableStateOf("") }

                    Row {
                        TextFieldUI(
                            modifier = Modifier.weight(1f).height(50.dp),
                            value = textUrl.value,
                            onValueChange = {
                                textUrl.value = it
                            }
                        )
                        Spacer(modifier = Modifier.width(8.dp))
                        IconButton(
                            onClick = {
                                coroutineScope.launch {
                                    jsonData.value = null
                                    jsonUrl.value = null
                                    delay(50)
                                    jsonUrl.value = textUrl.value
                                }
                            }
                        ) {
                            Image(
                                modifier = Modifier
                                    .clip(CircleShape)
                                    .size(32.dp),
                                imageVector = getRunIcon(),
                                contentDescription = null
                            )
                        }
                    }

                    Spacer(modifier = Modifier.height(48.dp))

                    TitleUI("Json")

                    Spacer(modifier = Modifier.height(8.dp))

                    val textJson = remember { mutableStateOf("") }

                    Row {
                        TextFieldUI(
                            modifier = Modifier.weight(1f).height(300.dp),
                            value = textJson.value,
                            onValueChange = {
                                textJson.value = it
                            }
                        )
                        Spacer(modifier = Modifier.width(8.dp))
                        IconButton(
                            onClick = {
                                coroutineScope.launch {
                                    jsonData.value = null
                                    delay(50)
                                    jsonData.value = textJson.value
                                }
                            }
                        ) {
                            Image(
                                modifier = Modifier
                                    .clip(CircleShape)
                                    .size(32.dp),
                                imageVector = getRunIcon(),
                                contentDescription = null
                            )
                        }
                    }
                }
            },
            rightComponent = {
                RightComponent(
                    json = jsonData
                )
            },
            dividerColor = Color.Gray,
            dividerWidth = 4.dp
        )
    }
}