package com.sefagurel.komposto.ui

import androidx.compose.foundation.layout.Box
import androidx.compose.foundation.layout.fillMaxSize
import androidx.compose.foundation.layout.padding
import androidx.compose.material.CircularProgressIndicator
import androidx.compose.runtime.Composable
import androidx.compose.runtime.State
import androidx.compose.ui.Alignment
import androidx.compose.ui.Modifier
import androidx.compose.ui.graphics.StrokeCap
import androidx.compose.ui.unit.dp
import com.sefagurel.komposto.ui.ui.HydraColor
import kompostoproject.komposto.ui.generated.resources.Res
import org.jetbrains.compose.resources.ExperimentalResourceApi
import org.jetbrains.compose.resources.vectorResource

@Composable
fun MainView(
    json: String? = null
) {
    App(json ?: SampleData.json)
}

@Composable
@OptIn(ExperimentalResourceApi::class)
fun getRunIcon() = vectorResource(Res.drawable.play_button_green_icon)

@Composable
fun KompostoWebPanel(
    json: State<String?>
) {
    DeviceFrame(
        modifier = Modifier.padding(8.dp)
    ) {

        json.value?.let {
            App(
                json = it
            )
        } ?: run {
            Loading()
        }
    }
}

@Composable
fun Loading() {
    Box(
        contentAlignment = Alignment.Center,
        modifier = Modifier.fillMaxSize()
    ) {
        CircularProgressIndicator(
            color = HydraColor.SeaBuckthorn,
            strokeWidth = 4.dp,
            strokeCap = StrokeCap.Round
        )
    }
}