package com.sefagurel.komposto.ui.component

import androidx.compose.foundation.layout.Box
import androidx.compose.foundation.layout.size
import androidx.compose.material.Icon
import androidx.compose.runtime.Composable
import androidx.compose.ui.Alignment
import androidx.compose.ui.Modifier
import androidx.compose.ui.unit.dp
import com.sefagurel.komposto.ui.extension.clickableNoRipple
import com.sefagurel.komposto.ui.ui.HydraColor
import kompostoproject.komposto.ui.generated.resources.Res
import org.jetbrains.compose.resources.ExperimentalResourceApi
import org.jetbrains.compose.resources.painterResource
import androidx.compose.desktop.ui.tooling.preview.Preview

@OptIn(ExperimentalResourceApi::class)
@Composable
internal fun HydraBackButton(
    modifier: Modifier = Modifier,
    onBackButtonClicked: () -> Unit
) {
    Box(
        modifier = modifier
            .size(40.dp)
            .clickableNoRipple(
                enabled = true,
                onClick = onBackButtonClicked
            ),
        contentAlignment = Alignment.Center
    ) {
        Icon(
            modifier = Modifier.size(
                width = 18.38.dp,
                height = 17.25.dp
            ),
            tint = HydraColor.BlackCow,
            painter = painterResource(Res.drawable.hy_ic_back),
            contentDescription = null
        )
    }
}

@Composable
@Preview
internal fun HydraBackButtonPreview() {
    HydraBackButton {

    }
}