val jsonKt = Json {
    encodeDefaults = false
//    explicitNulls = false
    ignoreUnknownKeys = true
    coerceInputValues = true
//    serializersModule = componentSerializersModule
}

internal inline fun <reified T> JsonElement.parse(): T {
    return jsonKt.decodeFromJsonElement(this)
}

internal inline fun <reified T> String.parse(): T {
    return jsonKt.decodeFromString(this)
}

@Serializable
@Polymorphic
sealed interface HydraData {

}

@Serializable
@Polymorphic
sealed interface HydraCustom {

}

@Serializable
data class BaseResponse<out T>(
    @SerialName("result")
    val result: T? = null,

    @SerialName("messages")
    val messages: List<JsonElement>? = null
)


fun parseHydraJson(json: String?): ComponentUiModel? {
    val model = json?.parse<BaseResponse<JsonObject>?>()
    return parseComponentUIModel(model?.result)
}

private fun parseComponentUIModel(jsonObj: JsonObject?): ComponentUiModel? {
    if (jsonObj == null) return null

    val type = jsonObj["type"]?.parse<UIType>()
    val base = jsonObj["base"]?.parse<BaseUiModel>()
    val data = jsonObj["data"]?.parseDataClass(type)
    val custom = jsonObj["custom"]?.parseCustomClass(type)
    val items =
        jsonObj["items"]?.jsonArray?.mapNotNull { it.jsonObject.let(::parseComponentUIModel) }

    return ComponentUiModel(
        type = type,
        base = base,
        data = data,
        custom = custom,
        items = items
    )
}

private fun JsonElement?.parseDataClass(uiType: UIType?): HydraData? {
    return when (uiType) {
        UIType.Label -> this?.parse<LabelDataUiModel>()
        UIType.Image -> this?.parse<ImageDataUiModel>()
        UIType.PosterBox -> this?.parse<PosterBoxDataUiModel>()
        UIType.Countdown -> this?.parse<CountdownDataUiModel>()
        UIType.CampaignCard -> this?.parse<CampaignCardDataModel>()
        UIType.Page -> this?.parse<PageDataUiModel>()
        UIType.Pager -> this?.parse<PagerListDataUiModel>()
        else -> null
    }
}

private fun JsonElement?.parseCustomClass(uiType: UIType?): HydraCustom? {
    return when (uiType) {
        UIType.VerticalList -> this?.parse<ColumnCustomUiModel>()
        UIType.Label -> this?.parse<LabelCustomUiModel>()
        UIType.Image -> this?.parse<ImageCustomUiModel>()
        UIType.HorizontalList -> this?.parse<RowCustomUiModel>()
        UIType.PosterBox -> this?.parse<PosterBoxCustomUiModel>()
        UIType.Countdown -> this?.parse<CountdownCustomUiModel>()
        UIType.Pager -> this?.parse<PagerListCustomUiModel>()
        else -> null
    }
}