@Composable
internal fun LabelUI(
    scope: Any? = null,
    uiModel: UIModel,
    onClick: (HydraClick) -> Unit,
    onEvent: (HydraEvent) -> Unit
) {
    val labelUiModel = uiModel.current ?: return
    val labelDataUiModel = labelUiModel.data as? LabelDataUiModel ?: return
    val labelCustomUiModel = labelUiModel.custom as? LabelCustomUiModel

    if (labelDataUiModel.text.isNullOrEmpty()) return

    val coroutineScope = rememberCoroutineScope()

    LaunchedEffect(key1 = Unit) {
        coroutineScope.launch {
            labelUiModel.base?.events?.find { it.type == HydraEventType.View }?.let { hydraEvent ->
                onEvent.invoke(hydraEvent)
            }
        }
    }

    val labelModifier = Modifier
        .fillMaxWidth()
        .addCommonModifiers(
            uiModel = uiModel,
            scope = scope,
            onClick = { hydraClick ->
                coroutineScope.launch {
                    labelUiModel.base?.events?.find { it.type == HydraEventType.Click }
                        ?.let { hydraEvent ->
                            onEvent.invoke(hydraEvent)
                        }
                }

                onClick.invoke(hydraClick)
            }
        )

//    if (labelCustomUiModel?.htmlFormatted == true) {
//        HBHtmlText(  // TODO: only android
//            html = labelDataUiModel.text,
//            maxLines = labelCustomUiModel.numberOfLines.getMaxLines(),
//            textStyle = TextStyle(
//                color = labelCustomUiModel.font?.color.toColor() ?: HydraColor.Black,
//                fontSize = labelCustomUiModel.font?.size?.sp ?: 18.sp,
//                fontWeight = labelCustomUiModel.font?.weight?.getUIFontWeight(),
//                fontStyle = labelCustomUiModel.font?.style?.getFontStyle(),
//                textDecoration = labelCustomUiModel.font?.decoration?.getTextDecoration(),
//                textAlign = labelCustomUiModel.textAlignment.getTextAlignment(),
//                fontFamily = labelCustomUiModel.font?.name?.getFontFamily()
//            ),
//            localGravity = labelCustomUiModel.textAlignment,
//            modifier = labelModifier
//        )
//    } else {
    Text(
        text = labelDataUiModel.text,
        color = labelCustomUiModel?.font?.color.toColor() ?: HydraColor.Black,
        fontSize = labelCustomUiModel?.font?.size?.sp ?: 18.sp,
        fontWeight = labelCustomUiModel?.font?.weight?.getUIFontWeight(),
        maxLines = labelCustomUiModel?.numberOfLines.getMaxLines(),
        fontStyle = labelCustomUiModel?.font?.style?.getFontStyle(),
        textDecoration = labelCustomUiModel?.font?.decoration?.getTextDecoration(),
        textAlign = labelCustomUiModel?.textAlignment.getTextAlignment(),
//        fontFamily = labelCustomUiModel?.font?.name?.getFontFamily(),
        modifier = labelModifier
    )
//    }
}