package com.sefagurel.komposto.ui.model

import kotlinx.serialization.SerialName
import kotlinx.serialization.Serializable

@Serializable
data class ColumnCustomUiModel(
    @SerialName("interItemSpacing")
    val interItemSpacing: Int? = null,
    @SerialName("horizontalAlignment")
    val horizontalAlignment: HorizontalAlignmentModel? = null,
    @SerialName("isScrollable")
    val scrollable: Boolean? = null
) : HydraCustom

@Serializable
data class RowCustomUiModel(
    @SerialName("interItemSpacing")
    val interItemSpacing: Int? = null,
    @SerialName("verticalAlignment")
    val verticalAlignmentModel: VerticalAlignmentModel? = null,
    @SerialName("isScrollable")
    val scrollable: Boolean? = null
) : HydraCustom

@Serializable
data class LabelCustomUiModel(
    @SerialName("htmlFormatted")
    val htmlFormatted: Boolean? = null,
    @SerialName("font")
    val font: FontUiModel? = null,
    @SerialName("numberOfLines")
    val numberOfLines: Int? = null,
    @SerialName("textAlignment")
    val textAlignment: TextAlignmentUiModel? = null
) : HydraCustom

@Serializable
data class ImageCustomUiModel(
    @SerialName("mode")
    val mode: ImageModeUiModel? = null,
) : HydraCustom

@Serializable
data class PosterBoxCustomUiModel(
    @SerialName("showImageBorder")
    val showImageBorder: Boolean? = null,
    @SerialName("fontSize")
    val fontSize: Int? = null,
    @SerialName("titleColor")
    val titleColor: String? = null,
    @SerialName("numberOfLines")
    val numberOfLines: Int? = null
) : HydraCustom

@Serializable
data class CountdownCustomUiModel(
    @SerialName("boxColor")
    val boxColor: String? = null,
    @SerialName("textColor")
    val textColor: String? = null,
    @SerialName("titleColor")
    val titleColor: String? = null
) : HydraCustom

@Serializable
data class PagerListCustomUiModel(
    @SerialName("indicatorColor")
    val indicatorColor: String? = null,
    @SerialName("isSearchable")
    val isSearchable: Boolean? = null
) : HydraCustom

@Serializable
enum class HorizontalAlignmentModel {
    @SerialName("left")
    LEFT,

    @SerialName("center")
    CENTER,

    @SerialName("right")
    RIGHT
}

@Serializable
enum class VerticalAlignmentModel {
    @SerialName("top")
    TOP,

    @SerialName("center")
    CENTER,

    @SerialName("bottom")
    BOTTOM
}

@Serializable
data class FontUiModel(
    @SerialName("color")
    val color: String? = null,
    @SerialName("name")
    val name: FontNameUiModel? = null,
    @SerialName("size")
    val size: Int? = null,
    @SerialName("style")
    val style: FontStyleUiModel? = null,
    @SerialName("decoration")
    val decoration: List<TextStyleUiModel>? = null,
    @SerialName("weight")
    val weight: FontWeightUiModel? = null,
)

@Serializable
enum class FontWeightUiModel {
    @SerialName("light")
    Light,

    @SerialName("regular")
    Regular,

    @SerialName("medium")
    Medium,

    @SerialName("bold")
    Bold
}

@Serializable
enum class TextStyleUiModel {
    @SerialName("underline")
    Underline,

    @SerialName("line-through")
    LineThrough
}

@Serializable
enum class ImageModeUiModel {
    @SerialName("fill")
    Fill,

    @SerialName("fit")
    Fit,

    @SerialName("center")
    Center
}

@Serializable
enum class FontNameUiModel {
    @SerialName("Inter")
    Inter
}

@Serializable
enum class FontStyleUiModel {
    @SerialName("italic")
    Italic,

    @SerialName("normal")
    Normal
}

@Serializable
enum class TextAlignmentUiModel {
    @SerialName("left")
    Left,

    @SerialName("center")
    Center,

    @SerialName("right")
    Right
}