package com.sefagurel.komposto.data.model

import com.sefagurel.komposto.data.Sn
import kotlinx.serialization.Polymorphic
import kotlinx.serialization.SerialName
import kotlinx.serialization.Serializable

@Serializable
@Polymorphic
sealed interface ComponentData {
    val modifiers: List<ModifierData>?
}

@Serializable
@SerialName(Sn.Box)
data class BoxData(

    @SerialName(Sn.Modifiers)
    override val modifiers: List<ModifierData>? = null,

    @SerialName(Sn.ContentAlignment)
    val contentAlignment: AlignmentData? = null,

    @SerialName(Sn.PropagateMinConstraints)
    val propagateMinConstraints: Boolean? = null,

    @SerialName(Sn.ComponentList)
    val componentList: List<ComponentData>? = null
) : ComponentData

@Serializable
@SerialName(Sn.Column)
data class ColumnData(

    @SerialName(Sn.Modifiers)
    override val modifiers: List<ModifierData>? = null,

    @SerialName(Sn.VerticalArrangement)
    val verticalArrangement: VerticalArrangementData? = null,

    @SerialName(Sn.HorizontalAlignment)
    val horizontalAlignment: HorizontalAlignmentData? = null,

    @SerialName(Sn.ComponentList)
    val componentList: List<ComponentData>? = null
) : ComponentData

@Serializable
@SerialName(Sn.LazyColumn)
data class LazyColumnData(

    @SerialName(Sn.Modifiers)
    override val modifiers: List<ModifierData>? = null,

    @SerialName(Sn.VerticalArrangement)
    val verticalArrangement: VerticalArrangementData? = null,

    @SerialName(Sn.HorizontalAlignment)
    val horizontalAlignment: HorizontalAlignmentData? = null,

    @SerialName(Sn.ComponentList)
    val componentList: List<ComponentData>? = null
) : ComponentData

@Serializable
@SerialName(Sn.Row)
data class RowData(

    @SerialName(Sn.Modifiers)
    override val modifiers: List<ModifierData>? = null,

    @SerialName(Sn.HorizontalArrangement)
    val horizontalArrangement: HorizontalArrangementData? = null,

    @SerialName(Sn.VerticalAlignment)
    val verticalAlignment: VerticalAlignmentData? = null,

    @SerialName(Sn.ComponentList)
    val componentList: List<ComponentData>? = null
) : ComponentData

@Serializable
@SerialName(Sn.LazyRow)
data class LazyRowData(

    @SerialName(Sn.Modifiers)
    override val modifiers: List<ModifierData>? = null,

    @SerialName(Sn.HorizontalArrangement)
    val horizontalArrangement: HorizontalArrangementData? = null,

    @SerialName(Sn.VerticalAlignment)
    val verticalAlignment: VerticalAlignmentData? = null,

    @SerialName(Sn.ComponentList)
    val componentList: List<ComponentData>? = null
) : ComponentData

@Serializable
@SerialName(Sn.Text)
data class TextData(
    @SerialName(Sn.Text)
    val text: String? = null,

    @SerialName(Sn.Modifiers)
    override val modifiers: List<ModifierData>? = null,

    @SerialName(Sn.Color)
    val color: String? = null,

    @SerialName(Sn.FontSize)
    val fontSize: Float? = null,

    @SerialName(Sn.FontStyle)
    val fontStyle: FontStyleData? = null,

    @SerialName(Sn.FontWeight)
    val fontWeight: Int? = null,

    @SerialName(Sn.FontFamily)
    val fontFamily: FontFamilyData? = null,

    @SerialName(Sn.LetterSpacing)
    val letterSpacing: Float? = null,

    @SerialName(Sn.TextDecoration)
    val textDecoration: TextDecorationData? = null,

    @SerialName(Sn.TextAlign)
    val textAlign: TextAlignData? = null,

    @SerialName(Sn.LineHeight)
    val lineHeight: Float? = null,

    @SerialName(Sn.Overflow)
    val overflow: TextOverflowData? = null,

    @SerialName(Sn.SoftWrap)
    val softWrap: Boolean? = null,

    @SerialName(Sn.MaxLines)
    val maxLines: Int? = null,

    @SerialName(Sn.MinLines)
    val minLines: Int? = null
) : ComponentData

@Serializable
@SerialName(Sn.Image)
data class ImageData(
    @SerialName(Sn.Url)
    val url: String? = null,

    @SerialName(Sn.ContentDescription)
    val contentDescription: String? = null,

    @SerialName(Sn.Modifiers)
    override val modifiers: List<ModifierData>? = null,

    @SerialName(Sn.Alignment)
    val alignment: AlignmentData? = null,

    @SerialName(Sn.ContentScale)
    val contentScale: ContentScaleData? = null,

    @SerialName(Sn.Alpha)
    val alpha: Float? = null
) : ComponentData