private const val ONE_SECOND = 1000L
private const val SIXTY = 60

@Composable
internal fun CountdownUI(
    uiModel: UIModel,
    stateAction: (HydraStateAction) -> Unit,
    timeDifferenceBetweenServerClient: Long?
) {
    if (timeDifferenceBetweenServerClient == null) return

    val countdownModel = uiModel.current ?: return
    val countdownDataModel = countdownModel.data as? CountdownDataUiModel ?: return
    val expireDate = countdownDataModel.expireDate ?: return
    val countdownCustomUiModel = countdownModel.custom as? CountdownCustomUiModel

    Column(
        modifier = Modifier.fillMaxWidth(),
        verticalArrangement = Arrangement.spacedBy(
            space = 8.dp,
            alignment = Alignment.CenterVertically
        ),
        horizontalAlignment = Alignment.CenterHorizontally
    ) {
        if (countdownDataModel.title.isNullOrEmpty().not()) {
            CountdownTitle(
                title = countdownDataModel.title.orEmpty(),
                titleColor = countdownCustomUiModel?.titleColor.orEmpty()
            )
        }
        if (countdownDataModel.expireDate.orZero > 0) {
            CountdownCounterSection(
                expireDate = expireDate,
                backgroundColor = countdownCustomUiModel?.boxColor.orEmpty(),
                textColor = countdownCustomUiModel?.textColor.orEmpty(),
                stateAction = stateAction,
                timeDifferenceBetweenServerClient = timeDifferenceBetweenServerClient
            )
        }
    }
}

@Composable
internal fun CountdownTitle(
    modifier: Modifier = Modifier,
    title: String,
    titleColor: String
) {
    Text(
        text = title,
        style = HydraTheme.font.h5Semibold().copy(
            color = titleColor.toColor() ?: HydraColor.Black,
            letterSpacing = (-0.03).sp
        ),
        modifier = modifier.widthIn(min = 24.dp)
    )
}

@Composable
internal fun CountdownCounterSection(
    modifier: Modifier = Modifier,
    expireDate: Long,
    backgroundColor: String,
    textColor: String,
    stateAction: ((HydraStateAction) -> Unit)?,
    timeDifferenceBetweenServerClient: Long
) {
    var isRefreshed by remember { mutableStateOf(false) }
    var currentTimeMillis by remember {
        mutableStateOf(
            Clock.System.now().toEpochMilliseconds() + timeDifferenceBetweenServerClient
        )
    }
    var timeDifference by remember { mutableStateOf(0L) }
    var hours by remember { mutableStateOf("00") }
    var minutes by remember { mutableStateOf("00") }
    var seconds by remember { mutableStateOf("00") }

    fun padZero(value: Long): String {
        return if (value < 10) "0$value" else "$value"
    }

    LaunchedEffect(expireDate, currentTimeMillis) {
        while (currentTimeMillis <= expireDate) {
            timeDifference = if (expireDate - currentTimeMillis <= 0) {
                0
            } else {
                expireDate - currentTimeMillis
            }

            hours = padZero(timeDifference / (60 * 60 * 1000))
            minutes = padZero((timeDifference / (60 * 1000)) % 60)
            seconds = padZero((timeDifference / 1000) % 60)

            delay(ONE_SECOND)
            currentTimeMillis =
                Clock.System.now().toEpochMilliseconds() + timeDifferenceBetweenServerClient
        }
    }

    LaunchedEffect(key1 = currentTimeMillis) {
        if (expireDate - currentTimeMillis <= 0 && isRefreshed.not()) {
            stateAction?.invoke(HydraStateAction.CountDownRefresh)
            isRefreshed = true
        }
    }


    Row(
        modifier = modifier,
        verticalAlignment = Alignment.CenterVertically,
        horizontalArrangement = Arrangement.spacedBy(
            space = 4.dp,
            alignment = Alignment.CenterHorizontally
        )
    ) {
        CountdownBox(
            type = CountdownType.HOUR,
            backgroundColor = backgroundColor,
            textColor = textColor,
            countdownValue = hours
        )
        CountdownCircle(color = backgroundColor)
        CountdownBox(
            type = CountdownType.MINUTE,
            backgroundColor = backgroundColor,
            textColor = textColor,
            countdownValue = minutes
        )
        CountdownCircle(color = backgroundColor)
        CountdownBox(
            type = CountdownType.SECOND,
            backgroundColor = backgroundColor,
            textColor = textColor,
            countdownValue = seconds
        )

    }
}

@Composable
internal fun CountdownBox(
    modifier: Modifier = Modifier,
    type: CountdownType,
    backgroundColor: String,
    textColor: String?,
    countdownValue: String
) {
    /**
     * Countdown component
     * This component can be show hours, minutes or second. It depends on passed "type" argument
     */
    Column(
        modifier = modifier
            .size(60.dp)
            .clip(RoundedCornerShape(6.dp))
            .border(
                border = BorderStroke(width = 1.dp, color = Color.White.copy(alpha = 0.8f)),
                shape = RoundedCornerShape(6.dp)
            )
            .background(
                color = backgroundColor.toColor() ?: HydraColor.BlazeOrange
            ),
        horizontalAlignment = Alignment.CenterHorizontally,
        verticalArrangement = Arrangement.spacedBy(
            space = 2.dp,
            alignment = Alignment.CenterVertically
        )
    ) {
        /**
         * Countdown value text
         * Eg: if countdown is 5 hours this text will show "5" in UI
         */
        Text(
            text = countdownValue,
            style = HydraTheme.font.h4Bold().copy(
                color = textColor.toColor() ?: HydraColor.White,
                letterSpacing = (-0.03).sp,
                textAlign = TextAlign.Center
            )
        )

        /**
         * Countdown type text
         * Eg: if countdown is 5 hours this text will show "hours" in UI
         */
        Text(
            text = type.getTitle(),
            style = HydraTheme.font.bodyTinyMedium().copy(
                color = textColor.toColor() ?: HydraColor.White,
                textAlign = TextAlign.Center
            )
        )
    }
}

@Composable
internal fun CountdownCircle(
    modifier: Modifier = Modifier,
    color: String
) {
    /**
     * Circle for separate each countdown box in ui
     */
    Column(
        modifier = modifier,
        verticalArrangement = Arrangement.spacedBy(
            space = 4.dp,
            alignment = Alignment.CenterVertically
        ),
        horizontalAlignment = Alignment.CenterHorizontally
    ) {
        repeat(2) {
            Box(
                modifier = Modifier
                    .size(4.dp)
                    .clip(CircleShape)
                    .background(color = color.toColor() ?: HydraColor.BlazeOrange)
            )
        }

    }
}

