package com.sefagurel.komposto.ui.model

import kotlinx.serialization.SerialName
import kotlinx.serialization.Serializable
import kotlinx.serialization.Transient

@Serializable
data class LabelDataUiModel(
    @SerialName("text")
    val text: String? = null
) : HydraData

@Serializable
data class ImageDataUiModel(
    @SerialName("url")
    val url: String? = null,
    @SerialName("altText")
    val altText: String? = null
) : HydraData

@Serializable
data class PosterBoxDataUiModel(
    @SerialName("imageUrl")
    val imageUrl: String? = null,
    @SerialName("text")
    val text: String? = null,
    @SerialName("altText")
    val altText: String? = null
) : HydraData

@Serializable
data class CountdownDataUiModel(
    @SerialName("expireDate")
    val expireDate: Long? = null,
    @SerialName("title")
    val title: String? = null
) : HydraData

@Serializable
data class PageDataUiModel(
    @SerialName("title")
    val title: String? = null,
    @SerialName("shareUrl")
    val shareUrl: String? = null,
    @SerialName("pageUrl")
    val pageUrl: String? = null
) : HydraData

@Serializable
data class CampaignCardDataModel(
    @SerialName("campaignType")
    val campaignType: CampaignType? = null,
    @SerialName("campaignDetail")
    val campaignDetail: CampaignDetail? = null,
    @SerialName("campaignButtonText")
    val campaignButtonText: String? = null,
    @SerialName("productId")
    val productId: String? = null,
    @SerialName("name")
    val name: String? = null,
    @SerialName("sku")
    val sku: String? = null,
    @SerialName("link")
    val link: String? = null,
    @SerialName("listingId")
    val listingId: String? = null,
    @SerialName("brand")
    val brand: Brand? = null,
    @SerialName("state")
    val state: Int? = null,
    @SerialName("imageUrls")
    val imageUrls: List<String>? = null,
    @SerialName("reviewsInfo")
    val reviewsInfo: ReviewsInfo? = null,
    @SerialName("stockInfo")
    val stockInfo: StockInfo? = null,
    @SerialName("priceInfo")
    val priceInfo: PriceInfo? = null,
    @SerialName("variantProperties")
    val variantProperties: VariantProperties? = null,
    @SerialName("labels")
    val labels: CampaignCardLabels? = null,
    @SerialName("altText")
    val altText: String? = null,
    @Transient val campaignCardAddToCartState: AddToCardState = AddToCardState.IDLE,
    @Transient val campaignCardFavoriteState: FavoriteState = FavoriteState.UNLIKE
) : HydraData

/**
 * Data class for brand field in CampaignCardDataUiModel.
 */
@Serializable
data class Brand(
    @SerialName("name")
    val name: String? = null,
    @SerialName("link")
    val link: String? = null,
)

/**
 * Enum class for campaignType field in CampaignCardDataUiModel.
 */
@Serializable
enum class CampaignType {
    @SerialName("product")
    Product,

    @SerialName("listing")
    Listing
}

/**
 * Data class for reviewsInfo field in CampaignCardDataUiModel.
 */
@Serializable
data class ReviewsInfo(
    @SerialName("rating")
    val rating: Float? = null,
    @SerialName("count")
    val count: String? = null,
)

/**
 * Data class for stockInfo field in CampaignCardDataUiModel.
 */
@Serializable
data class StockInfo(
    @SerialName("count")
    val count: String? = null
)

/**
 * Data class for priceInfo field in CampaignCardDataUiModel.
 * Prices can be Double or Int
 */
@Serializable
data class PriceInfo(
    @SerialName("originalPrice")
    val originalPrice: Double? = null,
    @SerialName("onSalePrice")
    val onSalePrice: Double? = null,
    @SerialName("premiumPrice")
    val premiumPrice: Double? = null
)

/**
 * Data class for variantProperties field in CampaignCardDataUiModel
 */
@Serializable
data class VariantProperties(
    @SerialName("hasVariant")
    val hasVariant: Boolean? = true
)

/**
 * Data class for labels field in CampaignCardDataUiModel
 */
@Serializable
data class CampaignCardLabels(
    @SerialName("badges")
    val badges: List<CampaignBadge>? = null,
    @SerialName("tagLabels")
    val tagLabels: List<TagLabel>? = null,
    @SerialName("productLabels")
    val productLabels: List<CampaignLabel>? = null,
)

/**
 * Data class for badges field in CampaignCardLabels
 */
@Serializable
data class CampaignBadge(
    @SerialName("url")
    val url: String? = null,
    @SerialName("tagName")
    val tagName: String? = null
)

/**
 * Data class for campaignLabels fields in CampaignCardLabels
 */
@Serializable
data class CampaignLabel(
    @SerialName("text")
    val text: String? = null,
    @SerialName("textColor")
    val textColor: String? = null,
    @SerialName("tagName")
    val tagName: String? = null
)

/**
 * Data class for tagLabels and productLabels fields in CampaignCardLabels
 */
@Serializable
data class TagLabel(
    @SerialName("text")
    val text: String? = null,
    @SerialName("backgroundColor")
    val backgroundColor: String? = null,
    @SerialName("tagName")
    val tagName: String? = null,
    @SerialName("textColor")
    val textColor: String? = null
)

/**
 * Data class for campaignDetail field in CampaignCardDataModel
 */
@Serializable
data class CampaignDetail(
    @SerialName("text")
    val text: String? = null,
    @SerialName("color")
    val color: String? = null
)

/**
 * Data Ui Model For PagerList Component
 */
@Serializable
data class PagerListDataUiModel(
    @SerialName("pageTitles")
