@Composable
internal fun PageUI(
    uiModel: UIModel,
    scope: Any?,
    onClick: (HydraClick) -> Unit,
    onEvent: (HydraEvent) -> Unit,
    stateAction: (HydraStateAction) -> Unit,
    timeDifferenceBetweenServerClient: Long?
) {
    val pageModel = uiModel.current ?: return
    val pageDataModel = pageModel.data as? PageDataUiModel

    val coroutineScope = rememberCoroutineScope()

    LaunchedEffect(key1 = Unit) {
        coroutineScope.launch {
            pageModel.base?.events?.find { it.type == HydraEventType.ScreenLoad }?.let {
                onEvent.invoke(it)
            }
        }
    }

    Column(
        modifier = Modifier
            .fillMaxSize()
            .background(pageModel.base?.backgroundColor.toColor() ?: HydraColor.White)
    ) {
        PageToolbar(
            title = pageDataModel?.title,
            shareUrl = pageDataModel?.shareUrl,
            pageUrl = pageDataModel?.pageUrl,
            onClick = onClick
        )
        pageModel.items?.forEach {
            HydraDynamicUI(
                uiModel = UIModel(it, uiModel.createParentModel()),
                scope = scope,
                onClick = onClick,
                onEvent = onEvent,
                stateAction = stateAction,
                timeDifferenceBetweenServerClient = timeDifferenceBetweenServerClient
            )
        }
    }
}


@Composable
internal fun PageToolbar(
    modifier: Modifier = Modifier,
    title: String?,
    shareUrl: String?,
    pageUrl: String?,
    onClick: (HydraClick) -> Unit
) {

    Row(
        modifier = modifier
            .fillMaxWidth()
            .heightIn(min = 48.dp)
            .background(HydraColor.White)
            .padding(vertical = 8.dp, horizontal = 4.dp),
        verticalAlignment = Alignment.CenterVertically,
        horizontalArrangement = Arrangement.spacedBy(
            space = 8.dp,
            alignment = Alignment.CenterHorizontally
        )
    ) {
        HydraBackButton {
            onClick.invoke(HydraClick.Back)
        }

        PageToolbarTitle(title = title.orEmpty(), modifier = Modifier.weight(1f))

        if (shareUrl.isNotNullOrEmpty()) {
            HydraShareButton(
                shareUrl = shareUrl.orEmpty(),
                onClick = onClick,
                onEvent = {

                }
            )
        } else {
            Spacer(modifier = Modifier.width(32.dp))
        }

    }
}

@Composable
internal fun PageToolbarTitle(
    modifier: Modifier = Modifier,
    title: String

) {
    Text(
        modifier = modifier,
        text = title,
        style = HydraTheme.font.bodySBold().copy(
            color = HydraColor.DarkJungleGreen,
            textAlign = TextAlign.Center
        ),
        maxLines = 1,
        overflow = TextOverflow.Ellipsis
    )
}

