package com.sefagurel.komposto.ui.component

import androidx.compose.foundation.Canvas
import androidx.compose.foundation.background
import androidx.compose.foundation.focusable
import androidx.compose.foundation.layout.Box
import androidx.compose.foundation.layout.fillMaxSize
import androidx.compose.material.CircularProgressIndicator
import androidx.compose.runtime.Composable
import androidx.compose.ui.Alignment
import androidx.compose.ui.Modifier
import androidx.compose.ui.graphics.Color
import androidx.compose.ui.graphics.StrokeCap
import androidx.compose.ui.graphics.drawscope.Stroke
import androidx.compose.ui.semantics.contentDescription
import androidx.compose.ui.semantics.semantics
import androidx.compose.ui.unit.Dp
import androidx.compose.ui.unit.dp
import com.sefagurel.komposto.ui.ui.HydraColor
import androidx.compose.desktop.ui.tooling.preview.Preview

@Preview
@Composable
fun HBLoadingPreview() {
    HBLoading()
}

@Composable
fun HBLoading(modifier: Modifier = Modifier, strokeWidth: Dp = 5.dp) {
    Box(
        modifier = Modifier
            .background(Color.Transparent)
            .then(modifier)
            .focusable(true)
            .semantics {
                contentDescription = "stek yaplyor"
            },
        contentAlignment = Alignment.Center
    ) {
        Canvas(
            modifier = Modifier.fillMaxSize().align(Alignment.Center)
        ) {
            val canvasWidth = size.width
            drawCircle(
                color = HydraColor.Whisper,
                radius = (canvasWidth / 2),
                style = Stroke(
                    width = strokeWidth.toPx()
                )
            )
        }
        CircularProgressIndicator(
            color = HydraColor.SeaBuckthorn,
            strokeWidth = strokeWidth,
            strokeCap = StrokeCap.Round
        )
    }
}
