package com.sefagurel.komposto.ui.model

import kotlinx.serialization.SerialName
import kotlinx.serialization.Serializable

@Serializable
data class BaseUiModel(
    @SerialName("size")
    val size: SizeUiModel? = null,
    @SerialName("margin")
    val margin: MarginUiModel? = null,
    @SerialName("padding")
    val padding: PaddingUiModel? = null,
    @SerialName("border")
    val border: BorderUiModel? = null,
    @SerialName("shadow")
    val shadow: ShadowUiModel? = null,
    @SerialName("tapAction")
    val tapActions: TapActionsUiModel? = null,
    @SerialName("backgroundColor")
    val backgroundColor: String? = null,
    @SerialName("events")
    val events: List<HydraEvent>? = null
)

@Serializable
data class SizeUiModel(
    @SerialName("width")
    val width: Int? = null,
    @SerialName("height")
    val height: Int? = null,
    @SerialName("aspect")
    val aspect: Float? = null,
    @SerialName("weight")
    val weight: Float? = null
)

@Serializable
data class MarginUiModel(
    @SerialName("top")
    val top: Int? = null,
    @SerialName("bottom")
    val bottom: Int? = null,
    @SerialName("left")
    val left: Int? = null,
    @SerialName("right")
    val right: Int? = null

)

@Serializable
data class PaddingUiModel(
    @SerialName("top")
    val top: Int? = null,
    @SerialName("bottom")
    val bottom: Int? = null,
    @SerialName("left")
    val left: Int? = null,
    @SerialName("right")
    val right: Int? = null
)

@Serializable
data class BorderUiModel(
    @SerialName("color")
    val color: String? = null,
    @SerialName("width")
    val width: Int? = null,
    @SerialName("cornerRadius")
    val cornerRadius: CornerRadiusUiModel? = null
)

@Serializable
data class CornerRadiusUiModel(
    @SerialName("topRight")
    val topRight: Int? = null,
    @SerialName("topLeft")
    val topLeft: Int? = null,
    @SerialName("bottomRight")
    val bottomRight: Int? = null,
    @SerialName("bottomLeft")
    val bottomLeft: Int? = null
)

@Serializable
data class ShadowUiModel(
    @SerialName("color")
    val color: String? = null,
    @SerialName("radius")
    val radius: Int? = null,
    @SerialName("opacity")
    val opacity: Float? = null
)

@Serializable
data class TapActionsUiModel(
    @SerialName("actionType")
    val actionType: ActionType? = null,
    @SerialName("command")
    val command: String? = null
)

@Serializable
enum class ActionType {
    @SerialName("apiCall")
    ApiCall,

    @SerialName("action")
    Action,

    @SerialName("navigation")
    Navigation
}

@Serializable
data class HydraEvent(
    @SerialName("type")
    val type: HydraEventType? = null,
    @SerialName("name")
    val name: String? = null,
    @SerialName("data")
    val data: String? = null
)

@Serializable
enum class HydraEventType {
    @SerialName("View")
    View,

    @SerialName("Click")
    Click,

    @SerialName("AddToCartClick")
    AddToCartClick,

    @SerialName("ListingOpportunityButtonClick")
    ListingOpportunityButtonClick,

    @SerialName("PagerClicked")
    PagerClicked,

    @SerialName("PagerView")
    PagerView,

    @SerialName("ScreenLoad")
    ScreenLoad
}