@Composable
internal fun CampaignCardUI(
    uiModel: UIModel,
    onClick: (HydraClick) -> Unit,
    onEvent: (HydraEvent) -> Unit
) {
    val campaignCardModel = uiModel.current
    val campaignCardDataModel = campaignCardModel?.data as? CampaignCardDataModel ?: return
    val campaignCardDataModelState =
        remember(campaignCardDataModel) { mutableStateOf(campaignCardDataModel) }
    val state = CampaignCardState from campaignCardDataModelState.value.state.orZero
    val parent = uiModel.parent
    val type = campaignCardDataModelState.value.campaignType ?: CampaignType.Product

    LaunchedEffect(key1 = parent?.searchTerm?.value) {
        campaignCardModel.base?.events?.find { it.type == HydraEventType.View }
            ?.sendEventWithSearchTerm(
                onEvent = onEvent,
                searchTerm = parent?.searchTerm?.value
            )
    }

    Box(
        Modifier
            .border(
                border = BorderStroke(width = 1.dp, color = HydraColor.BorderColor),
                shape = RoundedCornerShape(12.dp)
            )
            .clip(RoundedCornerShape(12.dp))
            .then(
                if (parent?.type == UIType.HorizontalList && parent.hasHorizontalScrollableParent.orFalse()) {
                    Modifier.width(336.dp)
                } else {
                    Modifier.widthIn(
                        min = 336.dp,
                        max = 400.dp
                    )
                }
            )
            .height(156.dp)
            .background(HydraColor.White)
            .then(
                if (campaignCardDataModelState.value.link.isNotNullOrEmpty()) {
                    Modifier.clickableNoRipple {
                        onClick.invoke(HydraClick.Deeplink(campaignCardDataModelState.value.link.orEmpty()))
                        campaignCardModel.base?.events
                            ?.find { it.type == HydraEventType.Click }
                            ?.let { hydraEvent ->
                                hydraEvent.sendEventWithSearchTerm(
                                    onEvent = onEvent,
                                    searchTerm = parent?.searchTerm?.value
                                )
                            }
                    }
                } else {
                    Modifier
                }
            )
    ) {
        Row(
            modifier = Modifier.padding(12.dp)
        ) {
            CampaignCardImage(
                imageUrl = campaignCardDataModelState.value.imageUrls?.firstOrNull().orEmpty(),
                campaignType = campaignCardDataModelState.value.campaignType
                    ?: CampaignType.Listing,
                badgeUrl = campaignCardDataModelState.value.labels?.badges?.firstOrNull()?.url,
                state = state
            )

            Spacer(modifier = Modifier.width(13.dp))

            Column(
                modifier = Modifier
                    .fillMaxWidth()
                    .heightIn(132.dp)
            ) {
                CampaignCardInfoSection(
                    campaignCardDataModel = campaignCardDataModelState.value,
                    modifier = Modifier.fillMaxWidth(),
                    onBrandNameClicked = onClick
                )

                Spacer(modifier = Modifier.height(2.dp))
                Spacer(modifier = Modifier.weight(1f))

                Column(
                    modifier = Modifier.fillMaxWidth(),
                    horizontalAlignment = Alignment.CenterHorizontally
                ) {
                    Spacer(modifier = Modifier.weight(1f))

                    CampaignCardButton(
                        buttonText = campaignCardDataModelState.value.getButtonText(),
                        state = state,
                        type = type,
                        addToCartState = campaignCardDataModelState.value.campaignCardAddToCartState,
                        favState = campaignCardDataModelState.value.campaignCardFavoriteState,
                        modifier = Modifier
                            .align(alignment = Alignment.CenterHorizontally)
                            .fillMaxWidth(),
                        onClick = {
                            campaignCardDataModelState.getHydraClickAndClickEventsForTypeAndState(
                                onClick = onClick,
                                onEvent = onEvent,
                                campaignBaseModel = campaignCardModel.base,
                                searchTerm = parent?.searchTerm
                            )
                        }
                    )
                }
            }
        }
        CampaignCardOverlay(state = state)
    }
}

@Composable
internal fun CampaignCardInfoSection(
    modifier: Modifier = Modifier,
    campaignCardDataModel: CampaignCardDataModel,
    onBrandNameClicked: (HydraClick) -> Unit
) {
    Column(modifier = modifier) {
        if (campaignCardDataModel.labels?.tagLabels?.firstOrNull()?.tagName?.isNotNullOrEmpty()
                .orFalse() && campaignCardDataModel.campaignType == CampaignType.Product
        ) {
            val tagLabel = campaignCardDataModel.labels?.tagLabels?.firstOrNull()
            TagLabel(
                text = tagLabel?.text.orEmpty(),
                backgroundColor = tagLabel?.backgroundColor,
                textColor = tagLabel?.textColor
            )
        }

        Spacer(modifier = Modifier.height(2.dp))

        val maxLine = if (campaignCardDataModel.campaignType == CampaignType.Product) 1 else 2

        CampaignCardProductName(
            productName = campaignCardDataModel.name,
            brandName = campaignCardDataModel.brand?.name,
            brandLink = campaignCardDataModel.brand?.link,
            maxLines = maxLine,
            onBrandNameClicked = onBrandNameClicked,
            state = CampaignCardState from campaignCardDataModel.state.orZero
        )


        Spacer(modifier = Modifier.height(2.dp))

        CampaignCardRatingAndStockSection(
            reviewRating = campaignCardDataModel.reviewsInfo?.rating,
            reviewCount = campaignCardDataModel.reviewsInfo?.count,
            stockCount = campaignCardDataModel.stockInfo?.count,
            type = campaignCardDataModel.campaignType ?: CampaignType.Product
        )


        if (campaignCardDataModel.campaignType == CampaignType.Listing) {
            CampaignDetailText(
                campaignText = campaignCardDataModel.campaignDetail?.text.orEmpty(),
                color = campaignCardDataModel.campaignDetail?.color.orEmpty()
            )
        }

        Spacer(modifier = Modifier.height(2.dp))

        if (campaignCardDataModel.campaignType == CampaignType.Product) {
            campaignCardDataModel.priceInfo?.let {
                CampaignCardPriceSection(priceInfo = it)
            }
            campaignCardDataModel.labels?.productLabels?.firstOrNull()?.let {
                CampaignCardCampaignLabel(labelText = it.text, textColor = it.textColor)
            }
        }
    }
}

@Composable
internal fun CampaignCardButton(
    modifier: Modifier,
    state: CampaignCardState,
    type: CampaignType,
    buttonText: String,
    onClick: () -> Unit,
    favState: FavoriteState,
    addToCartState: AddToCardState
) {
    when (state) {
        CampaignCardState.EXPIRED -> {
            CampaignCardStandardButton(
                buttonText = buttonText,
                isEnabled = false,
                onClick = onClick,
                modifier = modifier,
                addToCartState = addToCartState
            )
        }

        CampaignCardState.OUT_OF_STOCK -> {
            CampaignCardOutOfStockButton(
                buttonText = buttonText,
                modifier = modifier,
                isEnabled = false
            )
        }

        CampaignCardState.COMING -> {
            CampaignCardStandardButton(
                buttonText = buttonText,
                isEnabled = false,
                modifier = modifier
            )
        }

        CampaignCardState.ACTIVE -> {
            CampaignCardStandardButton(
                buttonText = buttonText,
                onClick = onClick,
                modifier = modifier,
                addToCartState = addToCartState
            )
        }
    }
}

@Composable
internal fun CampaignCardPriceSection(
    modifier: Modifier = Modifier,
    priceInfo: PriceInfo
) {
    val isOriginalPriceAvailable = priceInfo.originalPrice != null && priceInfo.originalPrice > 0.0
    val isPremiumPriceAvailable = priceInfo.premiumPrice != null && priceInfo.premiumPrice > 0.0
    val isOnSalePriceAvailable = priceInfo.onSalePrice != null && priceInfo.onSalePrice > 0.0

    Row(
        modifier = modifier,
        verticalAlignment = Alignment.CenterVertically,
        horizontalArrangement = Arrangement.spacedBy(
            space = 5.dp,
            alignment = Alignment.CenterHorizontally
        )
    ) {
        if (isOnSalePriceAvailable) {
            when {
                isPremiumPriceAvailable -> {
                    PremiumPriceText(price = priceInfo.premiumPrice.formatPrice())
                    RegularPriceText(price = priceInfo.onSalePrice.formatPrice())
                }

                isOriginalPriceAvailable -> {
                    GreenBoldPriceText(price = priceInfo.onSalePrice.formatPrice())
                    RegularPriceText(
                        price = priceInfo.originalPrice.formatPrice(),
                        isLineThrough = true
                    )
                }

                else -> GreenBoldPriceText(price = priceInfo.onSalePrice.formatPrice())
            }
        }
    }
}

