@Composable
internal fun CampaignCardProductName(
    modifier: Modifier = Modifier,
    brandName: String?,
    brandLink: String?,
    productName: String?,
    maxLines: Int,
    onBrandNameClicked: (HydraClick) -> Unit,
    state: CampaignCardState?
) {
    val annotatedString = buildAnnotatedString {
        if (brandName.isNotNullOrEmpty()) {
            pushStringAnnotation("Clickable", brandName.orEmpty())
            withStyle(style = SpanStyle(HydraColor.Orange)) {
                append("$brandName ")
            }
            pop()
        }
        append(productName.orEmpty())
    }

    ClickableText(
        text = annotatedString,
        style = HydraTheme.font.bodySSemibold().copy(
            color = HydraColor.BlackCow,
            letterSpacing = (-0.03).sp
        ),
        maxLines = maxLines,
        overflow = TextOverflow.Ellipsis,
        modifier = modifier,
        onClick = { offset ->
            val annotations =
                annotatedString.getStringAnnotations("Clickable", start = offset, end = offset)
            if (annotations.isNotEmpty() && brandLink.isNotNullOrEmpty() && state == CampaignCardState.ACTIVE) {
                onBrandNameClicked.invoke(HydraClick.Deeplink(brandLink.orEmpty()))
            }
        }
    )
}

