@OptIn(ExperimentalResourceApi::class)
@Composable
internal fun CampaignCardImage(
    imageUrl: String?,
    badgeUrl: String?,
    campaignType: CampaignType,
    state: CampaignCardState,
    modifier: Modifier = Modifier
) {

    val ratio = if (campaignType == CampaignType.Listing) 1f else 0.75f
    Box(
        modifier = modifier
            .width(100.dp)
            .height(132.dp)
            .clip(RoundedCornerShape(4.dp))
    ) {
        /**
         * Product Image
         */
        HydraImage(
            url = imageUrl.orEmpty(),
            contentDescription = null,
            modifier = Modifier
                .fillMaxWidth()
                .aspectRatio(ratio)
                .align(Alignment.Center),
            contentScale = ContentScale.Fit
        )

        /**
         * Campaign Badge Image
         */
        if (campaignType == CampaignType.Product && badgeUrl?.isNotNullOrEmpty().orFalse()) {
            CampaignCardBadge(
                badgeUrl = badgeUrl.orEmpty(),
                modifier = Modifier
            )
        }
        /**
         * Locked Icon
         */
        if (state == CampaignCardState.COMING) {
            Image(
                painter = painterResource(Res.drawable.hy_ic_locked),
                contentDescription = null,
                modifier = Modifier
                    .size(32.dp)
                    .align(Alignment.Center)
            )
        }
    }
}

