@Composable
internal fun CampaignCardStandardButton(
    modifier: Modifier = Modifier,
    buttonText: String,
    isEnabled: Boolean = true,
    onClick: () -> Unit = {},
    addToCartState: AddToCardState? = null
) {
    var buttonState by remember {
        mutableStateOf(AddToCardState.IDLE)
    }

    val isLoadingEnabled = buttonState == AddToCardState.LOADING
    val isBorderEnabled =
        buttonState != AddToCardState.SUCCESS && buttonState != AddToCardState.LOADING
    var backgroundColor: Color = HydraColor.White
    var textColor: Color = HydraColor.DarkJungleGreen
    var buttonTextForState: String = buttonText

    LaunchedEffect(key1 = addToCartState) {
        buttonState = addToCartState ?: AddToCardState.IDLE
        if (addToCartState == AddToCardState.SUCCESS || addToCartState == AddToCardState.ERROR) {
            delay(2000L)
            buttonState = AddToCardState.IDLE
        }
    }

    when (buttonState) {
        AddToCardState.SUCCESS -> {
            backgroundColor = HydraColor.IrishGreen
            textColor = HydraColor.White
            buttonTextForState = "Sepete Eklendi"
        }

        AddToCardState.LOADING -> {
            backgroundColor = HydraColor.WhiteSmoke
        }

        AddToCardState.ERROR -> {
            buttonTextForState = "Eklenmedi!"
            backgroundColor = HydraColor.BrightRed
            textColor = HydraColor.White
        }

        else -> Unit
    }

    Box(
        modifier = modifier
            .heightIn(min = 32.dp)
            .width(200.dp)
            .clip(RoundedCornerShape(8.dp))
            .then(
                if (isBorderEnabled) {
                    Modifier.border(
                        border = BorderStroke(2.dp, HydraColor.BorderColor),
                        shape = RoundedCornerShape(8.dp)
                    )
                } else {
                    Modifier
                }
            )
            .background(backgroundColor)
            .then(
                if (isEnabled) {
                    Modifier.clickable(
                        role = Role.Button,
                        onClick = onClick
                    )
                } else {
                    Modifier
                }
            )
            .padding(vertical = 6.dp, horizontal = 12.dp),
        contentAlignment = Alignment.Center
    ) {
        if (isLoadingEnabled.not()) {
            Text(
                text = buttonTextForState,
                style = HydraTheme.font.h6Semibold().copy(
                    color = textColor,
                    letterSpacing = (-0.03).sp,
                    textAlign = TextAlign.Center
                ),
                maxLines = 1
            )
        } else {
            HBLoading(
                modifier = Modifier.size(size = 20.dp),
                strokeWidth = 3.3.dp
            )
        }
    }
}

