internal fun CampaignCardOverlay(
    modifier: Modifier = Modifier,
    state: CampaignCardState
) {
    val overlayModifier = when (state) {
        CampaignCardState.ACTIVE -> {
            modifier.size(0.dp)
        }

        else -> {
            modifier.fillMaxSize()
        }
    }
    Box(
        modifier = overlayModifier
            .background(color = HydraColor.WhiteSmoke.copy(alpha = 0.3f))
    )
}

