internal fun MutableState<CampaignCardDataModel>.getHydraClickAndClickEventsForTypeAndState(
    onClick: (HydraClick) -> Unit,
    onEvent: (HydraEvent) -> Unit,
    campaignBaseModel: BaseUiModel?,
    searchTerm: MutableState<String?>?
) {
    val state = CampaignCardState from this.value.state.orZero
    val type = this.value.campaignType
    when {
        state == CampaignCardState.ACTIVE && type == CampaignType.Product -> {
            onClick.invoke(HydraClick.AddToCart(this))
            campaignBaseModel?.events?.find { it.type == HydraEventType.AddToCartClick }
                ?.let { hydraEvent ->
                    hydraEvent.sendEventWithSearchTerm(
                        onEvent = onEvent,
                        searchTerm = searchTerm?.value
                    )
                }
        }

        state == CampaignCardState.ACTIVE && type == CampaignType.Listing -> {
            this.value.link?.let { link ->
                onClick.invoke(HydraClick.Deeplink(link))
                campaignBaseModel?.events?.find { it.type == HydraEventType.ListingOpportunityButtonClick }
                    ?.let { hydraEvent ->
                        hydraEvent.sendEventWithSearchTerm(
                            onEvent = onEvent,
                            searchTerm = searchTerm?.value
                        )
                    }
            }
        }

        else -> {
            this.value.link?.let { link ->
                onClick.invoke(HydraClick.Deeplink(link))
                campaignBaseModel?.events?.find { it.type == HydraEventType.Click }
                    ?.let { hydraEvent ->
                        hydraEvent.sendEventWithSearchTerm(
                            onEvent = onEvent,
                            searchTerm = searchTerm?.value
                        )
                    }
            }
        }
    }
}

enum class CampaignCardState(val value: Int) {
    ACTIVE(0),
    COMING(1),
    EXPIRED(2),
    OUT_OF_STOCK(3);

    companion object {
        infix fun from(value: Int): CampaignCardState =
            CampaignCardState.values().firstOrNull { it.value == value } ?: ACTIVE
    }
}