package com.sefagurel.komposto.data.model

import com.sefagurel.komposto.data.Sn
import kotlinx.serialization.SerialName
import kotlinx.serialization.Serializable

@Serializable
@SerialName(Sn.TextAlign)
enum class TextAlignData {
    @SerialName(Sn.Left)
    Left,
    @SerialName(Sn.Right)
    Right,
    @SerialName(Sn.Center)
    Center,
    @SerialName(Sn.Justify)
    Justify,
    @SerialName(Sn.Start)
    Start,
    @SerialName(Sn.End)
    End
}

@Serializable
@SerialName(Sn.FontStyle)
enum class FontStyleData {
    @SerialName(Sn.Normal)
    Normal,
    @SerialName(Sn.Italic)
    Italic
}

@Serializable
@SerialName(Sn.FontFamily)
enum class FontFamilyData {
    @SerialName(Sn.Default)
    Default,
    @SerialName(Sn.SansSerif)
    SansSerif,
    @SerialName(Sn.Serif)
    Serif,
    @SerialName(Sn.Monospace)
    Monospace,
    @SerialName(Sn.Cursive)
    Cursive
}

@Serializable
@SerialName(Sn.TextDecoration)
enum class TextDecorationData {
    @SerialName(Sn.None)
    None,
    @SerialName(Sn.Underline)
    Underline,
    @SerialName(Sn.LineThrough)
    LineThrough
}

@Serializable
@SerialName(Sn.Overflow)
enum class TextOverflowData {
    @SerialName(Sn.Clip)
    Clip,
    @SerialName(Sn.Ellipsis)
    Ellipsis,
    @SerialName(Sn.Visible)
    Visible
}