package com.sefagurel.komposto.ui.model

import kotlinx.serialization.SerialName
import kotlinx.serialization.Serializable

@Serializable //(ComponentUiModelSerializer::class)
data class ComponentUiModel(
    @SerialName("type")
    val type: UIType? = null,
    @SerialName("base")
    val base: BaseUiModel? = null,
    @SerialName("data")
    val data: HydraData? = null,
    @SerialName("custom")
    val custom: HydraCustom? = null,
    @SerialName("items")
    val items: List<ComponentUiModel>? = null
)

@Serializable
enum class UIType {
    @SerialName("VerticalList")
    VerticalList,

    @SerialName("HorizontalList")
    HorizontalList,

    @SerialName("Label")
    Label,

    @SerialName("Image")
    Image,

    @SerialName("PosterBox")
    PosterBox,

    @SerialName("Countdown")
    Countdown,

    @SerialName("CampaignCard")
    CampaignCard,

    @SerialName("Page")
    Page,

    @SerialName("Pager") // TODO: @SerialName("PagerList")
    Pager;

    companion object {
        fun fromString(value: String): UIType {
            return valueOf(value)
        }
    }
}


