package com.sefagurel.komposto.ui.component

import androidx.compose.foundation.layout.Arrangement
import androidx.compose.foundation.layout.Row
import androidx.compose.foundation.lazy.LazyRow
import androidx.compose.foundation.lazy.rememberLazyListState
import androidx.compose.runtime.Composable
import androidx.compose.ui.Alignment
import androidx.compose.ui.Modifier
import androidx.compose.ui.unit.dp
import com.sefagurel.komposto.ui.HydraClick
import com.sefagurel.komposto.ui.HydraDynamicUI
import com.sefagurel.komposto.ui.HydraStateAction
import com.sefagurel.komposto.ui.extension.addCommonModifiers
import com.sefagurel.komposto.ui.extension.createParentModel
import com.sefagurel.komposto.ui.extension.getVerticalAlignment
import com.sefagurel.komposto.ui.extension.orFalse
import com.sefagurel.komposto.ui.extension.orZero
import com.sefagurel.komposto.ui.model.HydraEvent
import com.sefagurel.komposto.ui.model.RowCustomUiModel
import com.sefagurel.komposto.ui.model.UIModel

@Composable
internal fun RowUI(
    scope: Any? = null,
    uiModel: UIModel,
    onClick: (HydraClick) -> Unit,
    onEvent: (HydraEvent) -> Unit,
    stateAction: (HydraStateAction) -> Unit,
    timeDifferenceBetweenServerClient: Long?
) {
    val rowModel = uiModel.current ?: return
    val parentModel = uiModel.parent
    val rowCustom = rowModel.custom as? RowCustomUiModel

    DynamicRow(
        isLazy = parentModel?.hasHorizontalScrollableParent.orFalse()
            .not() && rowCustom?.scrollable.orFalse(),
        items = rowModel.items,
        verticalAlignment = rowCustom?.verticalAlignmentModel.getVerticalAlignment(),
        horizontalArrangement = Arrangement.spacedBy(
            rowCustom?.interItemSpacing.orZero.dp,
            Alignment.CenterHorizontally
        ),
        modifier = Modifier.addCommonModifiers(
            uiModel = uiModel,
            scope = scope,
            onClick = {
                onClick.invoke(it)
            }
        ),
    ) {
        HydraDynamicUI(
            uiModel = UIModel(it, uiModel.createParentModel()),
            scope = this,
            onClick = onClick,
            onEvent = onEvent,
            stateAction = stateAction,
            timeDifferenceBetweenServerClient = timeDifferenceBetweenServerClient
        )
    }

}

@Composable
internal fun <T> DynamicRow(
    modifier: Modifier = Modifier,
    isLazy: Boolean = false,
    horizontalArrangement: Arrangement.Horizontal = Arrangement.Start,
    verticalAlignment: Alignment.Vertical = Alignment.Top,
    items: List<T>? = null,
    content: @Composable Any.(item: T) -> Unit
) {

    if (isLazy) {
        LazyRow(
            state = rememberLazyListState(),
            horizontalArrangement = horizontalArrangement,
            verticalAlignment = verticalAlignment,
            modifier = modifier,
        ) {
            items?.forEach {
                item(
                    content = {
                        content.invoke(this, it)
                    }
                )
            }
        }

    } else {
        Row(
            horizontalArrangement = horizontalArrangement,
            verticalAlignment = verticalAlignment,
            modifier = modifier,
        ) {
            items?.forEach {
                content.invoke(this, it)
            }
        }

    }

}