package com.sefagurel.komposto.ui.component

import androidx.compose.runtime.Composable
import androidx.compose.runtime.LaunchedEffect
import androidx.compose.runtime.rememberCoroutineScope
import androidx.compose.ui.Modifier
import androidx.compose.ui.layout.ContentScale
import com.sefagurel.komposto.ui.HydraClick
import com.sefagurel.komposto.ui.extension.addCommonModifiers
import com.sefagurel.komposto.ui.extension.getImageScale
import com.sefagurel.komposto.ui.extension.sendEventWithSearchTerm
import com.sefagurel.komposto.ui.model.HydraEvent
import com.sefagurel.komposto.ui.model.HydraEventType
import com.sefagurel.komposto.ui.model.ImageCustomUiModel
import com.sefagurel.komposto.ui.model.ImageDataUiModel
import com.sefagurel.komposto.ui.model.UIModel
import kotlinx.coroutines.launch

@Composable
internal fun ImageUI(
    scope: Any? = null,
    uiModel: UIModel,
    onClick: (HydraClick) -> Unit,
    onEvent: (HydraEvent) -> Unit
) {
    val imageUiModel = uiModel.current ?: return
    val imageDataUiModel = imageUiModel.data as? ImageDataUiModel ?: return
    val imageCustomUiModel = imageUiModel.custom as? ImageCustomUiModel
    val searchTermState = uiModel.parent?.searchTerm
    val searchTermValue = searchTermState?.value

    val coroutineScope = rememberCoroutineScope()

    LaunchedEffect(key1 = searchTermValue) {
        coroutineScope.launch {
            imageUiModel.base?.events?.find { it.type == HydraEventType.View }?.let { hydraEvent ->
                hydraEvent.sendEventWithSearchTerm(
                    onEvent = onEvent,
                    searchTerm = searchTermState?.value
                )
            }
        }
    }

    HydraImage(
        url = imageDataUiModel.url.orEmpty(),
        contentDescription = null,
        contentScale = imageCustomUiModel?.mode?.getImageScale() ?: ContentScale.Fit,
        modifier = Modifier.addCommonModifiers(
            uiModel = uiModel,
            scope = scope,
            onClick = { hydraClick ->
                imageUiModel.base?.events?.find { it.type == HydraEventType.Click }
                    ?.let { hydraEvent ->
                        onEvent.invoke(hydraEvent)
                    }
                onClick.invoke(hydraClick)
            }
        )
    )

}