package com.sefagurel.komposto.data

import com.sefagurel.komposto.data.model.BoxData
import com.sefagurel.komposto.data.model.ColumnData
import com.sefagurel.komposto.data.model.ComponentData
import com.sefagurel.komposto.data.model.ImageData
import com.sefagurel.komposto.data.model.LazyColumnData
import com.sefagurel.komposto.data.model.LazyRowData
import com.sefagurel.komposto.data.model.RowData
import com.sefagurel.komposto.data.model.TextData
import kotlinx.serialization.json.Json
import kotlinx.serialization.modules.SerializersModule
import kotlinx.serialization.modules.polymorphic
import kotlinx.serialization.modules.subclass

private val componentSerializersModule = SerializersModule {
    polymorphic(ComponentData::class) {
        subclass(BoxData::class)
        subclass(ColumnData::class)
        subclass(LazyColumnData::class)
        subclass(RowData::class)
        subclass(LazyRowData::class)
        subclass(TextData::class)
        subclass(ImageData::class)
    }
}

val jsonKt = Json {
    encodeDefaults = false
    ignoreUnknownKeys = true
    coerceInputValues = true
    serializersModule = componentSerializersModule
}