package com.sefagurel.komposto.data.model

import com.sefagurel.komposto.data.Sn
import com.sefagurel.komposto.data.serializer.ArrangementDataSerializer
import com.sefagurel.komposto.data.serializer.HorizontalArrangementDataSerializer
import com.sefagurel.komposto.data.serializer.HorizontalOrVerticalArrangementDataSerializer
import com.sefagurel.komposto.data.serializer.VerticalArrangementDataSerializer
import kotlinx.serialization.SerialName
import kotlinx.serialization.Serializable

@Serializable(HorizontalArrangementDataSerializer::class)
@SerialName(Sn.HorizontalArrangement)
sealed interface HorizontalArrangementData

@Serializable(VerticalArrangementDataSerializer::class)
@SerialName(Sn.VerticalArrangement)
sealed interface VerticalArrangementData

@Serializable(HorizontalOrVerticalArrangementDataSerializer::class)
@SerialName(Sn.HorizontalOrVerticalArrangement)
sealed interface HorizontalOrVerticalArrangementData : HorizontalArrangementData, VerticalArrangementData

@Serializable(ArrangementDataSerializer::class)
@SerialName(Sn.Arrangement)
sealed interface ArrangementData {

    @Serializable
    @SerialName(Sn.Start)
    data object Start : HorizontalArrangementData, ArrangementData

    @Serializable
    @SerialName(Sn.End)
    data object End : HorizontalArrangementData, ArrangementData

    @Serializable
    @SerialName(Sn.TopEnd)
    data object TopEnd : ArrangementData

    @Serializable
    @SerialName(Sn.CenterStart)
    data object CenterStart : ArrangementData

    @Serializable
    @SerialName(Sn.Top)
    data object Top : VerticalArrangementData, ArrangementData

    @Serializable
    @SerialName(Sn.Bottom)
    data object Bottom : VerticalArrangementData, ArrangementData

    @Serializable
    @SerialName(Sn.Center)
    data object Center : HorizontalOrVerticalArrangementData

    @Serializable
    @SerialName(Sn.SpaceEvenly)
    data object SpaceEvenly : HorizontalOrVerticalArrangementData

    @Serializable
    @SerialName(Sn.SpaceBetween)
    data object SpaceBetween : HorizontalOrVerticalArrangementData

    @Serializable
    @SerialName(Sn.SpaceAround)
    data object SpaceAround : HorizontalOrVerticalArrangementData

    @Serializable
    @SerialName(Sn.SpacedBy)
    data class SpacedBy(
        val space: Float? = null
    ) : HorizontalOrVerticalArrangementData

    @Serializable
    @SerialName(Sn.SpacedByWithHorizontalAlignment)
    data class SpacedByWithHorizontalAlignment(
        val space: Float? = null,
        val alignment: HorizontalAlignmentData? = null
    ) : HorizontalArrangementData

    @Serializable
    @SerialName(Sn.SpacedByWithVerticalAlignment)
    data class SpacedByWithVerticalAlignment(
        val space: Float? = null,
        val alignment: VerticalAlignmentData? = null
    ) : VerticalArrangementData

    @Serializable
    @SerialName(Sn.AlignedHorizontal)
    data class AlignedHorizontal(
        val alignment: HorizontalAlignmentData? = null
    ) : HorizontalArrangementData

    @Serializable
    @SerialName(Sn.AlignedVertical)
    data class AlignedVertical(
        val alignment: VerticalAlignmentData? = null
    ) : VerticalArrangementData
}