package com.sefagurel.komposto.data.model

import com.sefagurel.komposto.data.Sn
import com.sefagurel.komposto.data.serializer.AlignmentDataSerializer
import com.sefagurel.komposto.data.serializer.HorizontalAlignmentDataSerializer
import com.sefagurel.komposto.data.serializer.VerticalAlignmentDataSerializer
import kotlinx.serialization.SerialName
import kotlinx.serialization.Serializable

@Serializable(HorizontalAlignmentDataSerializer::class)
@SerialName(Sn.HorizontalAlignment)
sealed interface HorizontalAlignmentData

@Serializable(VerticalAlignmentDataSerializer::class)
@SerialName(Sn.VerticalAlignment)
sealed interface VerticalAlignmentData

@Serializable(AlignmentDataSerializer::class)
@SerialName(Sn.Alignment)
sealed interface AlignmentData {

    @Serializable
    @SerialName(Sn.TopStart)
    data object TopStart : AlignmentData

    @Serializable
    @SerialName(Sn.TopCenter)
    data object TopCenter : AlignmentData

    @Serializable
    @SerialName(Sn.TopEnd)
    data object TopEnd : AlignmentData

    @Serializable
    @SerialName(Sn.CenterStart)
    data object CenterStart : AlignmentData

    @Serializable
    @SerialName(Sn.Center)
    data object Center : AlignmentData

    @Serializable
    @SerialName(Sn.CenterEnd)
    data object CenterEnd : AlignmentData

    @Serializable
    @SerialName(Sn.BottomStart)
    data object BottomStart : AlignmentData

    @Serializable
    @SerialName(Sn.BottomCenter)
    data object BottomCenter : AlignmentData

    @Serializable
    @SerialName(Sn.BottomEnd)
    data object BottomEnd : AlignmentData

    // 1D Alignment.Verticals.
    @Serializable
    @SerialName(Sn.Top)
    data object Top : VerticalAlignmentData

    @Serializable
    @SerialName(Sn.CenterVertically)
    data object CenterVertically : VerticalAlignmentData

    @Serializable
    @SerialName(Sn.Bottom)
    data object Bottom : VerticalAlignmentData

    // 1D Alignment.Horizontals.
    @Serializable
    @SerialName(Sn.Start)
    data object Start : HorizontalAlignmentData

    @Serializable
    @SerialName(Sn.CenterHorizontally)
    data object CenterHorizontally : HorizontalAlignmentData

    @Serializable
    @SerialName(Sn.End)
    data object End : HorizontalAlignmentData
}