@Composable
internal fun PagerListUI(
    uiModel: UIModel,
    scope: Any?,
    onClick: (HydraClick) -> Unit,
    onEvent: (HydraEvent) -> Unit,
    stateAction: (HydraStateAction) -> Unit,
    timeDifferenceBetweenServerClient: Long?
) {
    val pagerListUiModel = uiModel.current ?: return
    val pageItems = pagerListUiModel.items ?: return
    val pagerListDataUiModel = pagerListUiModel.data as? PagerListDataUiModel ?: return
    val pageTitles = pagerListDataUiModel.pageTitles ?: return
    val pagerListCustomUiModel = pagerListUiModel.custom as? PagerListCustomUiModel
    val isSearchable = pagerListCustomUiModel?.isSearchable.orFalse()
    val coroutineScope = rememberCoroutineScope()
    val searchTerm = remember { mutableStateOf<String?>(null) }

    if (pageItems.isEmpty() || pageTitles.isEmpty().orFalse()
        || pageItems.size != pageTitles.size
    ) return

    TabsLayoutWithPager(
        modifier = Modifier.fillMaxWidth(),
        indicatorColor = pagerListCustomUiModel?.indicatorColor.toColor() ?: HydraColor.Orange,
        pageTitles = pagerListDataUiModel.pageTitles,
        pageItems = pageItems,
        clickEvent = { tabIndex ->
            coroutineScope.launch {
                pagerListUiModel.base?.events?.filter { it.type == HydraEventType.PagerClicked }
                    ?.getOrNull(tabIndex)?.let { hydraEvent ->
                        onEvent.invoke(hydraEvent)
                    }
            }
        },
        viewEvent = { tabIndex ->
            coroutineScope.launch {
                pagerListUiModel.base?.events?.filter { it.type == HydraEventType.PagerView }
                    ?.getOrNull(tabIndex)?.let { hydraEvent ->
                        onEvent.invoke(hydraEvent)
                    }
            }
        },
        isSearchable = isSearchable,
        searchTerm = searchTerm

    ) {
        HydraDynamicUI(
            uiModel = UIModel(it, uiModel.createParentModel(searchTerm = searchTerm)),
            scope = scope,
            onClick = onClick,
            onEvent = onEvent,
            stateAction = stateAction,
            timeDifferenceBetweenServerClient = timeDifferenceBetweenServerClient
        )
    }
}

@OptIn(ExperimentalFoundationApi::class)
@Composable
internal fun TabsLayoutWithPager(
    modifier: Modifier = Modifier,
    indicatorColor: Color,
    pageTitles: List<String>,
    clickEvent: (Int) -> Unit,
    viewEvent: (Int) -> Unit,
    pageItems: List<ComponentUiModel>,
    isSearchable: Boolean,
    searchTerm: MutableState<String?>,
    content: @Composable Any.(item: ComponentUiModel) -> Unit,
) {

    val focusManager = LocalFocusManager.current

    val pagerState = rememberPagerState(
        initialPage = 0,
        initialPageOffsetFraction = 0f
    ) {
        pageTitles.size
    }

    val scope = rememberCoroutineScope()

    var searchedPage by remember { mutableStateOf<ComponentUiModel?>(null) }

    LaunchedEffect(searchTerm.value) {
        if (!pagerState.isScrollInProgress) {
            // Update searchedPage based on the new searchTerm
            val currentPage = pagerState.currentPage
            val currentSearchKeyword = searchTerm.value?.toLowerCase(Locale.current)?.removeSpaces()
            searchedPage = pageItems.getOrNull(currentPage)?.search(currentSearchKeyword.orEmpty())
        }
    }

    val isShouldClearSearch = remember {
        mutableStateOf(false)
    }

    LaunchedEffect(key1 = pagerState.currentPage) {
        isShouldClearSearch.value = pagerState.isScrollInProgress
        searchedPage = null
        isShouldClearSearch.value = true
    }

    Column(modifier = modifier.fillMaxWidth()) {
        ScrollableTabRow(
            modifier = Modifier.drawBehind {
                val borderWidth = 1.dp.toPx()

                drawLine(
                    color = HydraColor.BorderColor,
                    start = Offset(0f, size.height),
                    end = Offset(size.width, size.height),
                    strokeWidth = borderWidth
                )
            },
            selectedTabIndex = pagerState.currentPage,
            backgroundColor = Color.White,
            indicator = { tabPositions ->
                Box(
                    modifier = Modifier
                        .tabIndicatorOffset(tabPositions[pagerState.currentPage])
                        .height(3.dp)
                        .background(
                            color = indicatorColor,
                            shape = RoundedCornerShape(topStart = 2.dp, topEnd = 2.dp)
                        ),
                )
            },
            divider = {
                Divider(
                    color = Color.Transparent
                )
            },
            edgePadding = 0.dp
        ) {
            pageTitles.forEachIndexed { index, title ->
                Tab(
                    selected = pagerState.currentPage == index,
                    onClick = {
                        scope.launch {
                            pagerState.scrollToPage(index)
                            clickEvent.invoke(pagerState.currentPage)
                            isShouldClearSearch.value = false
                        }
                    },
                    text = {
                        Text(
                            text = title,
                            style = if (pagerState.currentPage != index) {
                                HydraTheme.font.bodyMRegular().copy(
                                    color = HydraColor.StarDust
                                )
                            } else {
                                HydraTheme.font.bodyMSemibold().copy(
                                    color = HydraColor.Black
                                )
                            }
                        )
                    }
                )
            }
        }

        if (isSearchable) {
            Spacer(modifier = Modifier.height(12.dp))

            SearchBar(
                modifier = Modifier.padding(horizontal = 12.dp),
                onSearch = { searchText ->
                    searchTerm.value = searchText
                },
                onSearchCleared = {
                    searchedPage = null
                    searchTerm.value = null
                },
                isShouldClearSearchbar = isShouldClearSearch
            )

            Spacer(modifier = Modifier.height(12.dp))
        }

        LaunchedEffect(pagerState.currentPage) {
            pageItems.getOrNull(pagerState.currentPage)?.let {
                viewEvent.invoke(pagerState.currentPage)
            }
        }

        HorizontalPager(
            state = pagerState,
            modifier = Modifier
                .fillMaxSize(),
            verticalAlignment = Alignment.Top
        ) {
            Box(modifier = Modifier.clickableNoRipple {
                focusManager.clearFocus()
            }) {
                if (searchedPage != null) {
                    searchedPage?.let {
                        content.invoke(this, it)
                    }
                } else {
                    pageItems.getOrNull(it)?.let {
                        content.invoke(this, it)
                    }
                }
            }
        }
    }
}

@OptIn(ExperimentalResourceApi::class)
@Composable
internal fun SearchBar(
    modifier: Modifier = Modifier,
    onSearch: (String) -> Unit,
    onSearchCleared: () -> Unit,
    isShouldClearSearchbar: MutableState<Boolean>
) {
    var text by remember { mutableStateOf("") }
    var isFocused by remember { mutableStateOf(false) }
    var job: Job? by remember { mutableStateOf(null) }
    val focusManager = LocalFocusManager.current

    LaunchedEffect(key1 = text) {
        job?.cancel() // lgili ii iptal et
        if (text.length >= 3) {
            job = launch {
                delay(300L)
                onSearch.invoke(text)
            }
        } else if (job != null) {
            job?.cancel() // Eer 3 karakterden az ise, ii iptal et
            onSearchCleared.invoke()
        }
    }

    if (isShouldClearSearchbar.value) {
        if (text.isBlank().not()) {
            text = ""
            focusManager.clearFocus()
        }
    }

    HydraOutlinedTextField(
        modifier = modifier
            .fillMaxWidth()
            .height(40.dp)
            .background(color = HydraColor.White, shape = RoundedCornerShape(8.dp))
            .onFocusChanged {
                isFocused = it.isFocused
            },
        value = text,
        singleLine = true,
        onValueChange = {
            text = it
            isShouldClearSearchbar.value = false
        },
        label = {
            if (isFocused.not() && text.isEmpty()) {
                Text(
                    text = stringResource(Res.string.hy_search_placeholder),
                    style = HydraTheme.font.bodySRegular().copy(
                        color = HydraColor.BlackCow
                    )
                )
            }
        },
        leadingIcon = {
            Icon(
                modifier = Modifier.size(20.dp),
                painter = painterResource(Res.drawable.hy_search_icon),
                contentDescription = null
            )
        },
        trailingIcon = {
            if (text.isNotNullOrEmpty()) {
                Icon(
                    modifier = Modifier
                        .size(20.dp)
                        .clickable {
                            text = ""
                            focusManager.clearFocus()
                        },
                    painter = painterResource(Res.drawable.hy_search_cancel_icon),
                    contentDescription = null
                )
            }
        },
        keyboardActions = KeyboardActions {
            focusManager.clearFocus()
        },
        colors = TextFieldDefaults.outlinedTextFieldColors(
            backgroundColor = HydraColor.White,
            cursorColor = HydraColor.DarkJungleGreen,
            focusedBorderColor = HydraColor.Orange,
            unfocusedBorderColor = HydraColor.BorderColor
        ),
        shape = RoundedCornerShape(8.dp),
        contentPaddingValues = PaddingValues(8.dp)
    )
}

private fun ComponentUiModel?.search(searchKeyword: String): ComponentUiModel? {
    val searchedList = mutableListOf<ComponentUiModel>()

    this?.items?.forEach { item ->
        when (item.type) {
            UIType.CampaignCard -> {
                val campaignCardData = item.data as? CampaignCardDataModel
                val text =
                    (campaignCardData?.brand?.name.orEmpty() + campaignCardData?.name.orEmpty() + campaignCardData?.campaignDetail?.text.orEmpty() + campaignCardData?.altText.orEmpty()).toLowerCase(
                        Locale.current
                    ).removeSpaces()
                if (text.contains(searchKeyword)) {
                    searchedList.add(item)
                }

            }

            UIType.Image -> {
                val imageData = item.data as? ImageDataUiModel
                val text = imageData?.altText.orEmpty().toLowerCase(Locale.current).removeSpaces()
                if (text.contains(searchKeyword).orFalse()) {
                    searchedList.add(item)
                }
            }

            UIType.PosterBox -> {
                val posterBoxData = item.data as? PosterBoxDataUiModel
                val text =
                    (posterBoxData?.text.orEmpty() + posterBoxData?.altText.orEmpty()).toLowerCase(
                        Locale.current
                    ).removeSpaces()
                if (text.contains(searchKeyword).orFalse()) {
                    searchedList.add(item)
                }
            }

            UIType.VerticalList, UIType.HorizontalList -> {
                val subSearchResult = item.search(searchKeyword)
                if (subSearchResult != null && subSearchResult.items?.isNotEmpty() == true) {
                    searchedList.add(subSearchResult)
                }
            }

            else -> Unit
        }
    }

    return this?.copy(
        items = searchedList
    )
}