@file:OptIn(ExperimentalSerializationApi::class)

package com.sefagurel.komposto.data.serializer

import com.sefagurel.komposto.data.Sn
import com.sefagurel.komposto.data.model.ArrangementData
import com.sefagurel.komposto.data.model.HorizontalAlignmentData
import com.sefagurel.komposto.data.model.HorizontalArrangementData
import com.sefagurel.komposto.data.model.HorizontalOrVerticalArrangementData
import com.sefagurel.komposto.data.model.VerticalAlignmentData
import com.sefagurel.komposto.data.model.VerticalArrangementData
import kotlinx.serialization.ExperimentalSerializationApi
import kotlinx.serialization.KSerializer
import kotlinx.serialization.SerializationException
import kotlinx.serialization.builtins.serializer
import kotlinx.serialization.descriptors.PrimitiveKind
import kotlinx.serialization.descriptors.PrimitiveSerialDescriptor
import kotlinx.serialization.descriptors.SerialDescriptor
import kotlinx.serialization.encoding.Decoder
import kotlinx.serialization.encoding.Encoder
import kotlinx.serialization.encoding.encodeStructure
import kotlinx.serialization.json.JsonDecoder
import kotlinx.serialization.json.decodeFromJsonElement
import kotlinx.serialization.json.jsonObject
import kotlinx.serialization.json.jsonPrimitive

object HorizontalArrangementDataSerializer : KSerializer<HorizontalArrangementData> {
    override val descriptor: SerialDescriptor =
        PrimitiveSerialDescriptor("HorizontalArrangementData", PrimitiveKind.STRING)

    override fun serialize(encoder: Encoder, value: HorizontalArrangementData) {
        when (value) {
            is ArrangementData.Start -> encoder.encodeString(Sn.Start)
            is ArrangementData.End -> encoder.encodeString(Sn.End)
            is ArrangementData.SpacedByWithHorizontalAlignment -> {
                encoder.encodeStructure(descriptor) {
                    encodeNullableSerializableElement(descriptor, 0, Float.serializer(), value.space)
                    encodeNullableSerializableElement(descriptor, 1, HorizontalAlignmentDataSerializer, value.alignment)
                }
            }
            is ArrangementData.AlignedHorizontal -> {
                encoder.encodeStructure(descriptor) {
                    encodeNullableSerializableElement(descriptor, 0, HorizontalAlignmentDataSerializer, value.alignment)
                }
            }

            else -> {}
        }
    }

    override fun deserialize(decoder: Decoder): HorizontalArrangementData {
        val input = decoder as? JsonDecoder ?: throw SerializationException("This class can be loaded only by Json")
        val element = input.decodeJsonElement()

        return when {
            element.jsonPrimitive.content == Sn.Start -> ArrangementData.Start
            element.jsonPrimitive.content == Sn.End -> ArrangementData.End
            element.jsonObject.containsKey("space") && element.jsonObject.containsKey("alignment") -> {
                val space = input.json.decodeFromJsonElement<Float?>(element.jsonObject["space"]!!)
                val alignment = input.json.decodeFromJsonElement<HorizontalAlignmentData?>(element.jsonObject["alignment"]!!)
                ArrangementData.SpacedByWithHorizontalAlignment(space, alignment)
            }
            element.jsonObject.containsKey("alignment") -> {
                val alignment = input.json.decodeFromJsonElement<HorizontalAlignmentData?>(element.jsonObject["alignment"]!!)
                ArrangementData.AlignedHorizontal(alignment)
            }
            else -> throw SerializationException("Unknown value: $element")
        }
    }
}

