object HorizontalOrVerticalArrangementDataSerializer: KSerializer<HorizontalOrVerticalArrangementData> {
    override val descriptor: SerialDescriptor =
        PrimitiveSerialDescriptor("HorizontalOrVerticalArrangementData", PrimitiveKind.STRING)

    override fun serialize(encoder: Encoder, value: HorizontalOrVerticalArrangementData) {
        when (value) {
            is ArrangementData.Center -> encoder.encodeString(Sn.Center)
            is ArrangementData.SpaceEvenly -> encoder.encodeString(Sn.SpaceEvenly)
            is ArrangementData.SpaceBetween -> encoder.encodeString(Sn.SpaceBetween)
            is ArrangementData.SpaceAround -> encoder.encodeString(Sn.SpaceAround)
            is ArrangementData.SpacedBy -> {
                encoder.encodeStructure(descriptor) {
                    encodeNullableSerializableElement(descriptor, 0, Float.serializer(), value.space)
                }
            }
        }
    }

    override fun deserialize(decoder: Decoder): HorizontalOrVerticalArrangementData {
        val input = decoder as? JsonDecoder ?: throw SerializationException("This class can be loaded only by Json")
        val element = input.decodeJsonElement()

        return when {
            element.jsonPrimitive.content == Sn.Center -> ArrangementData.Center
            element.jsonPrimitive.content == Sn.SpaceEvenly -> ArrangementData.SpaceEvenly
            element.jsonPrimitive.content == Sn.SpaceBetween -> ArrangementData.SpaceBetween
            element.jsonPrimitive.content == Sn.SpaceAround -> ArrangementData.SpaceAround
            element.jsonObject.containsKey("space") -> {
                val space = input.json.decodeFromJsonElement<Float?>(element.jsonObject["space"]!!)
                ArrangementData.SpacedBy(space)
            }
            else -> throw SerializationException("Unknown value: $element")
        }
    }
}

object ArrangementDataSerializer : KSerializer<ArrangementData> {
    override val descriptor: SerialDescriptor =
        PrimitiveSerialDescriptor("ArrangementData", PrimitiveKind.STRING)

    override fun serialize(encoder: Encoder, value: ArrangementData) {
        when (value) {
            is ArrangementData.Start -> encoder.encodeString(Sn.Start)
            is ArrangementData.End -> encoder.encodeString(Sn.End)
            is ArrangementData.TopEnd -> encoder.encodeString(Sn.TopEnd)
            is ArrangementData.CenterStart -> encoder.encodeString(Sn.CenterStart)
            is ArrangementData.Top -> encoder.encodeString(Sn.Top)
            is ArrangementData.Bottom -> encoder.encodeString(Sn.Bottom)
        }
    }

    override fun deserialize(decoder: Decoder): ArrangementData {
        val input = decoder as? JsonDecoder ?: throw SerializationException("This class can be loaded only by Json")
        val element = input.decodeJsonElement()

        return when {
            element.jsonPrimitive.content == Sn.Start -> ArrangementData.Start
            element.jsonPrimitive.content == Sn.End -> ArrangementData.End
            element.jsonPrimitive.content == Sn.TopEnd -> ArrangementData.TopEnd
            element.jsonPrimitive.content == Sn.CenterStart -> ArrangementData.CenterStart
            element.jsonPrimitive.content == Sn.Top -> ArrangementData.Top
            element.jsonPrimitive.content == Sn.Bottom -> ArrangementData.Bottom
            else -> throw SerializationException("Unknown value: $element")
        }
    }
}
