    Crop,

    @SerialName(Sn.Fit)
    Fit,

    @SerialName(Sn.FillHeight)
    FillHeight,

    @SerialName(Sn.FillWidth)
    FillWidth,

    @SerialName(Sn.Inside)
    Inside,

    @SerialName(Sn.None)
