package com.sefagurel.komposto.data.model

import com.sefagurel.komposto.data.Sn
import kotlinx.serialization.SerialName
import kotlinx.serialization.Serializable

@Serializable
@SerialName(Sn.Modifier)
sealed interface ModifierData {

    @Serializable
    @SerialName(Sn.Padding)
    data class Padding(
        @SerialName(Sn.Start)
        val start: Float? = null,
        @SerialName(Sn.Top)
        val top: Float? = null,
        @SerialName(Sn.End)
        val end: Float? = null,
        @SerialName(Sn.Bottom)
        val bottom: Float? = null
    ) : ModifierData

    @Serializable
    @SerialName(Sn.Shadow)
    data class Shadow(
        @SerialName(Sn.Elevation)
        val elevation: Float? = null,
        @SerialName(Sn.Shape)
        val shape: ShapeData? = null,
        @SerialName(Sn.Clip)
        val clip: Boolean? = null,
        @SerialName(Sn.AmbientColor)
        val ambientColor: String? = null,
        @SerialName(Sn.SpotColor)
        val spotColor: String? = null
    ) : ModifierData

    @Serializable
    @SerialName(Sn.WidthIn)
    data class WidthIn(
        @SerialName(Sn.Min)
        val min: Float? = null,
        @SerialName(Sn.Max)
        val max: Float? = null
    ) : ModifierData

    @Serializable
    @SerialName(Sn.Width)
    data class Width(
        @SerialName(Sn.Width)
        val width: Float? = null
    ) : ModifierData

    @Serializable
    @SerialName(Sn.FillMaxWidth)
    data object FillMaxWidth : ModifierData

    @Serializable
    @SerialName(Sn.HeightIn)
    data class HeightIn(
        @SerialName(Sn.Min)
        val min: Float? = null,
        @SerialName(Sn.Max)
        val max: Float? = null
    ) : ModifierData

    @Serializable
    @SerialName(Sn.Height)
    data class Height(
        @SerialName(Sn.Height)
        val height: Float? = null
    ) : ModifierData

    @Serializable
    @SerialName(Sn.FillMaxHeight)
    data object FillMaxHeight : ModifierData

    @Serializable
    @SerialName(Sn.Weight)
    data class Weight(
        @SerialName(Sn.Weight)
        val weight: Float? = null
    ) : ModifierData

    @Serializable
    @SerialName(Sn.AspectRatio)
    data class AspectRatio(
        @SerialName(Sn.Ratio)
        val ratio: Float? = null,
        @SerialName(Sn.MatchHeightConstraintsFirst)
        val matchHeightConstraintsFirst: Boolean? = null
    ) : ModifierData

    @Serializable
    @SerialName(Sn.Clip)
    data class Clip(
        @SerialName(Sn.Shape)
        val shape: ShapeData? = null
    ) : ModifierData

    @Serializable
    @SerialName(Sn.Border)
    data class Border(
        @SerialName(Sn.Width)
        val width: Float? = null,
        @SerialName(Sn.Color)
        val color: String? = null,
        @SerialName(Sn.Shape)
        val shape: ShapeData? = null
    ) : ModifierData

    @Serializable
    @SerialName(Sn.Background)
    data class Background(
        @SerialName(Sn.Color)
        val color: String? = null,
        @SerialName(Sn.Shape)
        val shape: ShapeData? = null
    ) : ModifierData

    @Serializable
    @SerialName(Sn.VerticalScroll)
    data class VerticalScroll(
        @SerialName(Sn.Enabled)
        val enabled: Boolean? = null,
        @SerialName(Sn.ReverseScrolling)
        val reverseScrolling: Boolean? = null
    ) : ModifierData

    @Serializable
    @SerialName(Sn.HorizontalScroll)
    data class HorizontalScroll(
        @SerialName(Sn.Enabled)
        val enabled: Boolean? = null,
        @SerialName(Sn.ReverseScrolling)
        val reverseScrolling: Boolean? = null
    ) : ModifierData

    @Serializable
    @SerialName(Sn.Size)
    data class Size(
        @SerialName(Sn.Width)
        val width: Float? = null,
        @SerialName(Sn.Height)
        val height: Float? = null
    ) : ModifierData
}
