package com.sefagurel.komposto.ui

import androidx.compose.runtime.Composable
import androidx.compose.runtime.LaunchedEffect
import androidx.compose.runtime.MutableState
import androidx.compose.runtime.getValue
import androidx.compose.runtime.mutableStateOf
import androidx.compose.runtime.remember
import androidx.compose.runtime.setValue
import com.sefagurel.komposto.ui.component.CampaignCardUI
import com.sefagurel.komposto.ui.component.ColumnUI
import com.sefagurel.komposto.ui.component.CountdownUI
import com.sefagurel.komposto.ui.component.ImageUI
import com.sefagurel.komposto.ui.component.LabelUI
import com.sefagurel.komposto.ui.component.PageUI
import com.sefagurel.komposto.ui.component.PagerListUI
import com.sefagurel.komposto.ui.component.PosterBoxUI
import com.sefagurel.komposto.ui.component.RowUI
import com.sefagurel.komposto.ui.model.CampaignCardDataModel
import com.sefagurel.komposto.ui.model.ComponentUiModel
import com.sefagurel.komposto.ui.model.HydraEvent
