import com.sefagurel.komposto.ui.model.parseHydraJson
import com.sefagurel.komposto.ui.theme.AppTheme

@Composable
fun DynamicComponent(
    json: String?,
    onClick: (HydraClick) -> Unit,
    onEvent: (HydraEvent) -> Unit,
    stateAction: (HydraStateAction) -> Unit,
    timeDifferenceBetweenServerClient: Long? = null
) = AppTheme {
    if (json == null) return@AppTheme

    var component by remember { mutableStateOf<ComponentUiModel?>(null) }

    LaunchedEffect(key1 = json) {
//        component = json.parse<BaseResponse<ComponentUiModel>>().result //TODO: only android
        component = runCatching {
            parseHydraJson(json)
        }.getOrNull()
    }

    HydraDynamicUI(
        uiModel = UIModel(component, null),
        onClick = onClick,
        onEvent = onEvent,
        stateAction = stateAction,
        timeDifferenceBetweenServerClient = timeDifferenceBetweenServerClient
    )
}

@Composable
internal fun HydraDynamicUI(
    uiModel: UIModel,
    scope: Any? = null,
    onClick: (HydraClick) -> Unit,
    onEvent: (HydraEvent) -> Unit,
    stateAction: (HydraStateAction) -> Unit,
    timeDifferenceBetweenServerClient: Long? = null
) {
    if (uiModel.current == null) return

    when (uiModel.current.type) {
        UIType.VerticalList -> ColumnUI(
            uiModel = uiModel,
            scope = scope,
            onClick = onClick,
            onEvent = onEvent,
            stateAction = stateAction,
            timeDifferenceBetweenServerClient = timeDifferenceBetweenServerClient
        )

        UIType.HorizontalList -> RowUI(
            uiModel = uiModel,
            scope = scope,
            onClick = onClick,
            onEvent = onEvent,
            stateAction = stateAction,
            timeDifferenceBetweenServerClient = timeDifferenceBetweenServerClient
        )

        UIType.Label -> LabelUI(
            uiModel = uiModel,
            scope = scope,
            onClick = onClick,
            onEvent = onEvent
        )

        UIType.Image -> ImageUI(
            uiModel = uiModel,
            scope = scope,
            onClick = onClick,
            onEvent = onEvent
        )

        UIType.PosterBox -> PosterBoxUI(
            uiModel = uiModel,
            scope = scope,
            onClick = onClick,
            onEvent = onEvent
        )

        UIType.CampaignCard -> CampaignCardUI(
            uiModel = uiModel,
            onClick = onClick,
            onEvent = onEvent
        )

        UIType.Countdown -> CountdownUI(
            uiModel = uiModel,
            stateAction = stateAction,
            timeDifferenceBetweenServerClient = timeDifferenceBetweenServerClient
        )

        UIType.Page -> PageUI(
            uiModel = uiModel,
            scope = scope,
            onClick = onClick,
            onEvent = onEvent,
            stateAction = stateAction,
            timeDifferenceBetweenServerClient = timeDifferenceBetweenServerClient
        )

        UIType.Pager -> PagerListUI(
            uiModel = uiModel,
            scope = scope,
            onClick = onClick,
            onEvent = onEvent,
            stateAction = stateAction,
            timeDifferenceBetweenServerClient = timeDifferenceBetweenServerClient
        )

        else -> Unit
    }
}

sealed class HydraClick {

    data class Deeplink(val deeplink: String) : HydraClick()

    data class AddToCart(
        val state: MutableState<CampaignCardDataModel>
    ) : HydraClick()

    data class AddOrRemoveFavorites(
        val state: MutableState<CampaignCardDataModel>
    ) : HydraClick()

    data class Share(val url: String) : HydraClick()

    object Back : HydraClick()

}

sealed class HydraStateAction {
    object CountDownRefresh : HydraStateAction()
}