package com.sefagurel.komposto.ui.component

import androidx.compose.foundation.layout.Arrangement
import androidx.compose.foundation.layout.Column
import androidx.compose.foundation.layout.fillMaxWidth
import androidx.compose.foundation.lazy.LazyColumn
import androidx.compose.foundation.lazy.rememberLazyListState
import androidx.compose.runtime.Composable
import androidx.compose.ui.Alignment
import androidx.compose.ui.Modifier
import androidx.compose.ui.unit.dp
import com.sefagurel.komposto.ui.HydraClick
import com.sefagurel.komposto.ui.HydraDynamicUI
import com.sefagurel.komposto.ui.HydraStateAction
import com.sefagurel.komposto.ui.extension.addCommonModifiers
import com.sefagurel.komposto.ui.extension.createParentModel
import com.sefagurel.komposto.ui.extension.getHorizontalAlignment
import com.sefagurel.komposto.ui.extension.orFalse
import com.sefagurel.komposto.ui.extension.orZero
import com.sefagurel.komposto.ui.model.ColumnCustomUiModel
import com.sefagurel.komposto.ui.model.HydraEvent
import com.sefagurel.komposto.ui.model.UIModel
import com.sefagurel.komposto.ui.model.UIType

@Composable
internal fun ColumnUI(
    scope: Any? = null,
    uiModel: UIModel,
    onClick: (HydraClick) -> Unit,
    onEvent: (HydraEvent) -> Unit,
    stateAction: (HydraStateAction) -> Unit,
    timeDifferenceBetweenServerClient: Long?
) {
    val columnUiModel = uiModel.current ?: return
    val parentUiModel = uiModel.parent
    val columnCustomModel = columnUiModel.custom as? ColumnCustomUiModel
    val isScrollable = columnCustomModel?.scrollable.orFalse()

    DynamicColumn(
        isLazy = parentUiModel?.hasVerticalScrollableParent.orFalse().not() && isScrollable,
        items = columnUiModel.items,
        horizontalAlignment = columnCustomModel?.horizontalAlignment.getHorizontalAlignment(),
        verticalArrangement = Arrangement.spacedBy(
            space = columnCustomModel?.interItemSpacing.orZero.dp,
            alignment = Alignment.CenterVertically
        ),
        modifier = Modifier
            .addCommonModifiers(
                uiModel = uiModel,
                scope = scope,
                onClick = onClick
            )
            .then(
                if (parentUiModel?.type == UIType.Page) {
                    Modifier.fillMaxWidth()
                } else {
                    Modifier
                }
            )
    ) {
        HydraDynamicUI(
            uiModel = UIModel(it, uiModel.createParentModel()),
            scope = this,
            onClick = onClick,
            onEvent = onEvent,
            stateAction = stateAction,
            timeDifferenceBetweenServerClient = timeDifferenceBetweenServerClient
        )
    }

}

@Composable
internal fun <T> DynamicColumn(
    modifier: Modifier = Modifier,
    isLazy: Boolean = false,
    verticalArrangement: Arrangement.Vertical = Arrangement.Top,
    horizontalAlignment: Alignment.Horizontal = Alignment.Start,
    items: List<T>? = null,
    content: @Composable Any.(item: T) -> Unit
) {

    if (isLazy) {
        LazyColumn(
            state = rememberLazyListState(),
            horizontalAlignment = horizontalAlignment,
            verticalArrangement = verticalArrangement,
            modifier = modifier,
        ) {
            items?.forEach {
                item {
                    content.invoke(this, it)
                }
            }
        }

    } else {
        Column(
            horizontalAlignment = horizontalAlignment,
            verticalArrangement = verticalArrangement,
            modifier = modifier,
        ) {
            items?.forEach {
                content.invoke(this, it)
            }
        }

    }

}