package com.sefagurel.komposto.ui.extension

import androidx.compose.runtime.Composable
import androidx.compose.ui.Alignment
import androidx.compose.ui.layout.ContentScale
import androidx.compose.ui.text.font.FontFamily
import androidx.compose.ui.text.font.FontStyle
import androidx.compose.ui.text.font.FontWeight
import androidx.compose.ui.text.style.TextAlign
import androidx.compose.ui.text.style.TextDecoration
import com.sefagurel.komposto.ui.model.FontNameUiModel
import com.sefagurel.komposto.ui.model.FontStyleUiModel
import com.sefagurel.komposto.ui.model.FontWeightUiModel
import com.sefagurel.komposto.ui.model.HorizontalAlignmentModel
import com.sefagurel.komposto.ui.model.ImageModeUiModel
import com.sefagurel.komposto.ui.model.TextAlignmentUiModel
import com.sefagurel.komposto.ui.model.TextStyleUiModel
import com.sefagurel.komposto.ui.model.VerticalAlignmentModel
import com.sefagurel.komposto.ui.ui.Inter

internal fun HorizontalAlignmentModel?.getHorizontalAlignment(): Alignment.Horizontal =
    when (this) {
        HorizontalAlignmentModel.CENTER -> Alignment.CenterHorizontally
        HorizontalAlignmentModel.RIGHT -> Alignment.End
        else -> Alignment.Start
    }

internal fun VerticalAlignmentModel?.getVerticalAlignment(): Alignment.Vertical =
    when (this) {
        VerticalAlignmentModel.BOTTOM -> Alignment.Bottom
        VerticalAlignmentModel.CENTER -> Alignment.CenterVertically
        else -> Alignment.Top
    }

internal fun FontWeightUiModel.getUIFontWeight(): FontWeight =
    when (this) {
        FontWeightUiModel.Light -> FontWeight.Light
        FontWeightUiModel.Medium -> FontWeight.Medium
        FontWeightUiModel.Bold -> FontWeight.Bold
        else -> FontWeight.Normal
    }

internal fun List<TextStyleUiModel?>?.getTextDecoration(): TextDecoration {
    val textDecorationList = mutableListOf<TextDecoration>()
    this?.forEach {
        when (it) {
            TextStyleUiModel.LineThrough -> textDecorationList.add(TextDecoration.LineThrough)
            TextStyleUiModel.Underline -> textDecorationList.add(TextDecoration.Underline)
            else -> TextDecoration.None
        }
    }
    return TextDecoration.combine(textDecorationList)
}

internal fun FontStyleUiModel?.getFontStyle(): FontStyle =
    when (this) {
        FontStyleUiModel.Italic -> FontStyle.Italic
        else -> FontStyle.Normal
    }

internal fun ImageModeUiModel?.getImageScale(): ContentScale =
    when (this) {
        /**
         * SwiftUi don't have "center" content mode. For align with iOS Team we will be act same for "fill" and "center" content modes
         */
        ImageModeUiModel.Fill, ImageModeUiModel.Center -> ContentScale.Crop
        else -> ContentScale.Fit
    }

@Composable
internal fun FontNameUiModel?.getFontFamily(): FontFamily =
    when (this) {
        //Inter is default font family, other font families will be added later.
        FontNameUiModel.Inter -> Inter()
        else -> Inter()
    }

internal fun TextAlignmentUiModel?.getTextAlignment(): TextAlign =
    when (this) {
        TextAlignmentUiModel.Center -> TextAlign.Center
        TextAlignmentUiModel.Left -> TextAlign.Start
        TextAlignmentUiModel.Right -> TextAlign.End
        else -> TextAlign.Start
    }