@OptIn(ExperimentalResourceApi::class)
@Composable
fun Inter() = FontFamily(
    Font(Res.font.inter_light, FontWeight.Thin), // W100
    Font(Res.font.inter_light, FontWeight.ExtraLight), // W200
    Font(Res.font.inter_light, FontWeight.Light), // W300
    Font(Res.font.inter_regular, FontWeight.Normal), // W400
    Font(Res.font.inter_medium, FontWeight.Medium), // W500
    Font(Res.font.inter_semi_bold, FontWeight.SemiBold), // W600
    Font(Res.font.inter_bold, FontWeight.Bold), // W700
    Font(Res.font.inter_bold, FontWeight.ExtraBold), // W800
    Font(Res.font.inter_bold, FontWeight.Black) // W900
)

