internal fun String?.toColor(): Color? {
    return runCatching {
        val strValue = this?.trim()
        if (strValue.isNullOrEmpty()) return null
        val colorString = if (strValue.firstOrNull() == '#') strValue else "#$strValue"
        if (colorString.matches(Regex("^#(?:[0-9a-fA-F]{6}|[0-9a-fA-F]{8})\$")).not()) return null
        var color = colorString.substring(1).toLong(16)
        if (colorString.length == 7) { // Set the alpha value
            color = color or -0x1000000
        } else {
            if (strValue.length != 9) return null
        }
        return Color(color.toInt())
    }.getOrNull()
}

internal val Int?.orZero: Int
    get() = this ?: 0

internal val Long?.orZero: Long
    get() = this ?: 0L

internal val Float?.orZero: Float
    get() = this ?: 0.0f

internal val Double?.orZero: Double
    get() = this ?: 0.0

internal val Dp?.orZero: Dp
    get() = this ?: 0.dp

internal fun Any?.isNull(): Boolean = this == null

internal fun Boolean?.orFalse(): Boolean = this ?: false
internal fun Modifier.addModifier(
    predicate: Boolean? = true,
    modifier: Modifier.() -> Modifier
): Modifier {
    return then(
        if (predicate == true) {
            modifier.invoke(Modifier)
        } else {
            Modifier
        }
    )
}

internal fun Modifier.clickableNoRipple(
    enabled: Boolean = true,
    onClickLabel: String? = null,
    role: Role? = null,
    onClick: () -> Unit
) = composed {
    clickable(
        indication = null,
        interactionSource = remember { MutableInteractionSource() },
        enabled = enabled,
        onClickLabel = onClickLabel,
        role = role,
        onClick = onClick
    )
}

internal fun Double?.formatPrice(): String {
    val number = this ?: return "0,00"
    val multiplied = kotlin.math.round(number * 100)
    val integerPart = (multiplied / 100).toLong()
    val decimalPart = (multiplied % 100).toInt()
    val integerPartFormatted = integerPart.toString()
        .reversed().chunked(3).joinToString(".").reversed()
    val decimalPartFormatted = decimalPart.toString().padStart(2, '0')
    return "$integerPartFormatted,$decimalPartFormatted"
}

internal fun String?.isNotNullOrEmpty(): Boolean {
    return this.isNullOrEmpty().not()
}

fun String?.clearHtmlTags(): String {
    val htmlTags = listOf(
        "<b>",
        "</b>",
        "<strike>",
        "</strike>",
        "<ul>",
        "</ul>",
        "<strong>",
        "</strong>",
        "<p>",
        "</p>"
    )

    htmlTags.forEach {
        this.orEmpty().replace(it, "")
    }

    return this.orEmpty()
}


internal fun UIModel.createParentModel(searchTerm: MutableState<String?>? = null): ParentModel {
    val searchTermParam =
        if (this.parent?.searchTerm.isNull().not()) this.parent?.searchTerm else searchTerm
    val columnCustomModel = this.current?.custom as? ColumnCustomUiModel
    val rowCustomModel = this.current?.custom as? RowCustomUiModel

    val isHasVerticalScrollableParent = this.parent?.hasVerticalScrollableParent.orFalse() ||
            (this.current?.type == UIType.VerticalList && columnCustomModel?.scrollable.orFalse())

    val isHasHorizontalScrollableParent = this.parent?.hasHorizontalScrollableParent.orFalse() ||
            (this.current?.type == UIType.HorizontalList && rowCustomModel?.scrollable.orFalse())

    val interItemSpacing = when (this.current?.type) {
        UIType.VerticalList -> columnCustomModel?.interItemSpacing.orZero
        UIType.HorizontalList -> rowCustomModel?.interItemSpacing.orZero
        else -> 0
    }

    return ParentModel(
        hasHorizontalScrollableParent = isHasHorizontalScrollableParent,
        hasVerticalScrollableParent = isHasVerticalScrollableParent,
        interItemSpacing = interItemSpacing,
        type = this.current?.type,
        padding = this.current?.base?.padding,
        searchTerm = searchTermParam
    )
}

internal fun Int?.getMaxLines(): Int {
    return when (this) {
        0 -> Int.MAX_VALUE
        null -> 1
        else -> this
    }
}

@OptIn(ExperimentalResourceApi::class)
@Composable
internal fun CampaignCardDataModel.getButtonText(): String {
    /**
     * ACTIVE = 0; Product Type: Button (Sepete Ekle) is Active, Listing Type: Button is Active
     * COMING = 1; Product Type: Lock Icon, Overlay and Button (Listene Ekle, Saati Bekle) is Active(image overlay), Listing: Lock Icon, Overlay, Button (Frsat Karma) is Passive(full overlay)
     * EXPIRED = 2; Product Type: Overlay, Button (Frsat Kat) is Passive, Listing: Overlay, Button (Frsat Kat) is Passive (full overlay)
     * OUT OF STOCK = 3; Product Type: Overlay, Button (Tkendi text with red color) is Passive, Overlay, Buton(Tkendi text with red color) is Passive (full overlay)
     */

    if (this.campaignButtonText.isNotNullOrEmpty()) return this.campaignButtonText.orEmpty()

    val state = CampaignCardState from this.state.orZero
    return when (state) {
        CampaignCardState.ACTIVE -> {
            if (this.campaignType == CampaignType.Listing) {
                stringResource(Res.string.hy_dont_miss_the_chance)
            } else {
                stringResource(Res.string.hy_add_to_cart)
            }
        }

        CampaignCardState.COMING -> {
            if (this.campaignType == CampaignType.Listing) {
                stringResource(Res.string.hy_dont_miss_the_chance)
            } else {
                stringResource(Res.string.hy_add_to_list)
            }
        }

        CampaignCardState.EXPIRED -> {
            stringResource(Res.string.hy_missed_chance)
        }

        CampaignCardState.OUT_OF_STOCK -> {
            stringResource(Res.string.hy_out_of_stock)
        }
    }
}

internal fun Modifier.init() = this.then(object : Modifier.Element {})

internal fun String.removeSpaces(): String {
    return this.filter { !it.isWhitespace() }
}

internal fun String?.addSearchTermValue(
    value: String
): String {
    val jsonElement = jsonKt.parseToJsonElement(this ?: "{}")
    val updatedJsonData = when (jsonElement) {
        is JsonObject -> jsonElement.toMutableMap().apply {
            put("searchTerm", JsonPrimitive(value))
        }

        else -> buildJsonObject { put("searchTerm", value) }
    }
    return jsonKt.encodeToString(JsonObject.serializer(), JsonObject(updatedJsonData))
}

